import { join } from "path";
import { readFileSync } from "fs";
import { ILoggingService, LogLevel } from "../logging";
import { modelFolder } from "../../resolvedPath";
import { LanguageId } from "../../utils/language";
import INormalizationService, {
  ParsedCode,
} from "./normalizationService";
import { TreeSitterManager } from "./treeSitterManager";
import * as Parser from "web-tree-sitter";

const pythonValue = [["integer", "float"], ["string"], [], []];
const typescriptValue = [
  ["number"],
  ["string", "template_string"],
  [],
  ["regex_pattern"],
];
const javascriptValue = [
  ["number"],
  ["string", "template_string"],
  [],
  ["regex_pattern"],
];
const javaValue = [
  [
    "decimal_floating_point_literal",
    "decimal_integer_literal",
    "hex_floating_point_literal",
    "hex_integer_literal",
    "octal_integer_literal",
    "binary_integer_literal",
  ],
  ["string_literal"],
  ["character_literal"],
  [],
];

//@ts-ignore
const lang2lits: { [key in LanguageId]: string[][] } = {};
lang2lits[LanguageId.PYTHON] = pythonValue;
lang2lits[LanguageId.JAVASCRIPT] = javascriptValue;
lang2lits[LanguageId.JAVASCRIPT_REACT] = javascriptValue;
lang2lits[LanguageId.TYPESCRIPT] = typescriptValue;
lang2lits[LanguageId.TYPESCRIPT_REACT] = typescriptValue;
lang2lits[LanguageId.JAVA] = javaValue;

type litKeys = "num" | "str" | "char" | "regex";

export async function initNormalizationService(logger: ILoggingService) {
  let treeSitterManager = await TreeSitterManager.buildTreeSitterManager(
    logger
  );
  let normalizationService = new TreeSitterNormalizationService(
    join(modelFolder, "TopLiterals.json"),
    {},
    [" ", ","],
    logger,
    treeSitterManager
  );
  return normalizationService;
}

export class TreeSitterNormalizationService implements INormalizationService {
  private literals: { [key in litKeys]: string[] };
  private lang2lits = lang2lits;
  private keywords = ["int", "integer", "float", "string", "char", "character"];

  private specialTokensMap: { [key: string]: string };
  private specialTokensMapReverse: { [key: string]: string } = {};
  private unicodeCharsMap: { [key: string]: any } = {};
  private unicodeCharsMapReverse: { [key: string]: string } = {};

  /**
   *
   * @param Logger Instance of a LoggingService
   * @param literalsFile Path to literals file
   * @param specialTokensMap Any tokens that need to be mapped in a specific way
   * @param unicodeChars Any characters that are in the output of the model as unicode. ex) " " -> U+0020 in output
   */
  public constructor(
    literalsFile = join(modelFolder, "TopLiterals.json"),
    specialTokensMap: { [key: string]: string } = {},
    unicodeChars: string[] = [" ", ","],
    private Logger: ILoggingService,
    private TreeSitterManager: TreeSitterManager
  ) {
    this.literals = this.prepareLiterals(literalsFile);
    this.specialTokensMap = specialTokensMap;

    if (specialTokensMap != null) {
      for (const z in specialTokensMap) {
        if (specialTokensMap.hasOwnProperty(z)) {
          this.specialTokensMapReverse[specialTokensMap[z]] = z;
        }
      }
    }
    if (unicodeChars != null) {
      for (const z of unicodeChars) {
        const numBer = z.charCodeAt(0);
        const mapValue = "U+" + numBer.toString(16).padStart(4, "0");
        this.unicodeCharsMap[z] = mapValue;
        this.unicodeCharsMapReverse[mapValue] = z;
      }
    }
  }

  /**
   * Masks literals with the appropriate tokens, and fully removes comments.
   * Will return empty string if currently within a comment or a string literal.
   *
   * @param code The code to normalize
   * @param lang The language of the code
   * @returns Normalized code or empty string
   */
  public normalize(parsedCode: ParsedCode): string | undefined {
    try {
      return this.normalizeInner(
        parsedCode.tree,
        parsedCode.code,
        parsedCode.lang,
        "remove",
        true
      );
    } catch (e) {
      this.Logger.write(
        LogLevel.ERROR,
        `[TreeSitterNormalizationService.normalize()] Failed to normalize code\n\tReason: ${e}`
      );
    }
    return undefined;
  }

  public parseAndNormalize(
    unparsedCode: string,
    lang: string
  ): string | undefined {
    try {
      const parse = this.parse(unparsedCode, lang);
      return this.normalize(parse);
    } catch (e) {
      this.Logger.write(
        LogLevel.ERROR,
        `[TreeSitterNormalizationService.parseAndNormalize()] Failed to normalize code\n\tReason: ${e}`
      );
    }
  }

  /**
   * Initializes the language specific portion of the service.
   * @param lang The language to initialize a Tree Sitter for
   */
  public async initialize(lang: string) {
    if (!this.checkSupport(lang)) {
      throw Error(
        `[TreeSitterNormalizationService.initialize()] Unsupported Language: ${lang}`
      );
    }
    return await this.TreeSitterManager.initialize(lang as LanguageId);
  }

  /**
   * Supported languages:
   *  Java, Javascript, Python, Typescript
   * If target language is a supported language,
   * this will return false until the tree-sitter is ready.
   *
   * Will always return false for unsupported languages.
   * @param lang LanguageId to check
   * @returns Whether passed language can be normalized or not
   */
  public isAvailableFor(lang: string): boolean {
    return (
      this.checkSupport(lang) !== undefined &&
      this.TreeSitterManager.isAvailableFor(lang as LanguageId)
    );
  }

  /**
   * This function is exposed solely for debugging purposes,
   * and shouldn't need to be called in the code.
   *
   * @param code The code to parse as a tree
   * @param lang The language of the code
   */
  public parse(code: string, langStr: string): ParsedCode {
    const lang = this.checkSupport(langStr);

    if (!lang) {
      throw Error(
        `[TreeSitterNormalizationService.normalize()] Unaccepted Language: ${langStr}`
      );
    }
    try {
      const parser = this.TreeSitterManager.createParser();
      parser.setLanguage(this.TreeSitterManager.get(lang));
      return {
        tree: parser.parse(code),
        code: code,
        lang: lang,
      };
    } catch (e) {
      this.Logger.write(
        LogLevel.ERROR,
        `[TreeSitterNormalizationService.normalize()] Failed to normalize code\n\tReason: ${e}`
      );
      throw e;
    }
  }

  private checkSupport(lang: string): LanguageId | undefined {
    const index = (<string[]>Object.values(LanguageId)).indexOf(lang);
    if (index >= 0) {
      return Object.values(LanguageId)[index];
    }
    return undefined;
  }

  private prepareLiterals(literalFilePath: string) {
    const literals: { [key in litKeys]: string[] } = JSON.parse(
      readFileSync(literalFilePath, "utf-8")
    );

    const arr = ["num", "str", "char", "regex"];
    for (const value of arr) {
      if (!literals.hasOwnProperty(value)) {
        literals[value as litKeys] = [];
      }
    }
    return literals;
  }

  /**
   * Normalize code with option to
   *  1) Normalize comments. All comments show up as a #<COMMENT> token.
   *  2) Keep comments. All comments show up as is.
   *  3) Remove comments. Comments are fully removed.
   *
   * Returns empty string if
   *  1) comment isn't set to "keep" and we're currently typing a comment
   *  2) currently typing within a string literal
   * @param code code to normalize
   * @param lang the language of the code
   * @param comment "remove" | "normalize" | "keep" defaults to "remove"
   * @param indent defaults to true. Creates <INDENT> tokens when normalizing
   * @returns Normalized code or empty string.
   */
  private normalizeInner(
    tree: Parser.Tree,
    code: string,
    lang: LanguageId,
    comment = "remove",
    indent = false
  ): string | undefined {
    const root = tree.rootNode;
    let tokens: string[] = [];
    let types: string[] = [];
    this.get_tokens(root, tokens, types);
    this.negativeNumberHandling(tokens, types, lang2lits[lang][0]);

    const pts = this._file_tokenizer(code, tokens, types);
    const poss = pts[0];
    tokens = pts[1];
    types = pts[2];

    /**
     * Quick stop scenario
     * 1. No recommendation within comments
     */
    if (comment !== "keep") {
      // Normal scenario
      if (types[types.length - 1] === "comment") {
        return undefined;
      }

      // Edge case for JS-based languages
      if (
        [
          LanguageId.JAVASCRIPT,
          LanguageId.JAVASCRIPT_REACT,
          LanguageId.TYPESCRIPT,
          LanguageId.TYPESCRIPT_REACT,
        ].indexOf(lang) !== -1
      ) {
        /**
         * Tree-sitter for JavaScript automatically creates a closing curly braces,
         * even when it's not in the source.
         * All languages described above have tree-sitters built off of tree-sitter javascript,
         * and share the same problem.
         */
        let lastNoneBracesIdx = types.length - 1;
        while (
          types[lastNoneBracesIdx] === "}" &&
          tokens[lastNoneBracesIdx] === "" &&
          lastNoneBracesIdx > -1
        ) {
          lastNoneBracesIdx -= 1;
        }
        if (lastNoneBracesIdx > -1 && types[lastNoneBracesIdx] === "comment") {
          return undefined;
        }
      }
    }

    /**
     * Quick stop scenario
     * 2. No recommendation within string literals
     */
    if (
      types.indexOf('"') !== -1 ||
      types.indexOf("'") !== -1 ||
      types.indexOf("`") !== -1
    ) {
      /**
       * FYI)
       * The different tree-sitters per language have different behavior.
       *
       * JS/TS:
       *   All code written after an open quotation mark will be ignored by the tree-sitter.
       *   When there's an open quotation mark, the last type returned, types[types.length - 1],
       *   will always be the quotation mark.
       *
       * PYTHON:
       *   Code written after an open quotation mark will still be parsed.
       *   There will just be a quotation mark somewhere in the types array.
       *
       * JAVA:
       *   Instead of keeping the unmatched quotation mark, it will utilize an ERROR token.
       *   This current logic cannot support JAVA behavior.
       */
      return undefined;
    }

    return this.norm_untokenize(
      poss,
      tokens,
      types,
      this.lang2lits[lang],
      this.literals,
      comment,
      indent
    );
  }

  /**
   * Handles negative numbers showing up as -<NUM_LIT>
   * @param tokens tokens
   * @param types types
   * @param numericLits lang2lits[lang][0]
   * @returns
   */
  private negativeNumberHandling(
    tokens: any,
    types: any,
    numericLits: string[]
  ) {
    for (let i = 0; i < types.length; i++) {
      if (types[i] === "-") {
        this.handleOneNegativeCase(tokens, types, i, numericLits);
      }
    }
  }

  private handleOneNegativeCase(
    tokens: any,
    types: any,
    hyphenIdx: number,
    numericLits: string[]
  ) {
    if (!numericLits.includes(types[hyphenIdx + 1])) {
      return false;
    }
    if (
      this.isNumericOperator(types[hyphenIdx - 1]) ||
      types[hyphenIdx - 1] == "," ||
      types[hyphenIdx - 1] == "(" ||
      types[hyphenIdx - 1] == "{" ||
      types[hyphenIdx - 1] == "["
    ) {
      // This is a negative number and not a subtraction
      tokens[hyphenIdx][1] = tokens[hyphenIdx + 1][1];
      types[hyphenIdx] = types[hyphenIdx + 1];
      for (let i = hyphenIdx + 1; i < types.length - 1; i++) {
        tokens[i] = tokens[i + 1];
        types[i] = types[i + 1];
      }
      tokens.pop();
      types.pop();
      return true;
    }
    return false;
  }

  private isNumericOperator(word: string) {
    return (
      word == "=" ||
      word == "==" ||
      word == "===" ||
      word == "!=" ||
      word == "!==" ||
      word == "+" ||
      word == "-" ||
      word == "*" ||
      word == "/" ||
      word == "%" ||
      word == "!" ||
      word == "not"
    );
  }

  private _file_tokenizer(
    code2: string,
    positions: any[],
    types: string[],
    keepNewline: boolean = true
  ) {
    const code: string[] = code2.split("\n");
    let prevLine: number = 0;
    const retPos: any[] = [];
    const retCode: any[] = [];
    const retType: any[] = [];
    const keyRow = "row";
    const keyCol = "column";
    for (let i = 0; i < positions.length; i++) {
      const token = positions[i];
      const sp = [token[0][keyRow], token[0][keyCol]];
      const ep = [token[1][keyRow], token[1][keyCol]];
      if (sp[0] !== prevLine && keepNewline) {
        retPos.push([]);
        retCode.push("\n");
        retType.push("new_line");
      }
      prevLine = ep[0];
      if (types[i] === "preproc_arg") {
        // This occurs in C++ after #defines and other preprocs
        // Everything after the identifier is thrown in here,
        // hence requires separate processing
        retPos.push(token);
        retType.push(types[i]);

        // This will at least get rid of comments
        retCode.push(
          code[sp[0]]
            .substring(sp[1], ep[1])
            .split("//")[0]
            .replace(/\/\*(.|\n)*\*\//g, "")
        );
      } else if (sp[0] === ep[0]) {
        retPos.push(token);
        retCode.push(code[sp[0]].substring(sp[1], ep[1]));
        retType.push(types[i]);
      } else {
        let out = code[sp[0]].substring(sp[1]);
        for (let lineid = sp[0] + 1; lineid < ep[0]; lineid++) {
          out += code[lineid];
        }
        out += code[ep[0]].substring(0, ep[1]);
        retPos.push(token);
        retCode.push(out);
        retType.push(types[i]);
      }
    }
    // Manually check for empty final line
    if (
      code[code.length - 1].trim() === "" &&
      keepNewline &&
      retCode[retCode.length - 1] !== "\n"
    ) {
      retPos.push([]);
      retCode.push("\n");
      retType.push("new_line");
    }
    return [retPos, retCode, retType];
  }

  private get_tokens(node: any, tokens: any, types: any) {
    if (node.children.length === 0) {
      tokens.push([node.startPosition, node.endPosition]);
      types.push(node.type);
      return;
    }
    const specificTypes: string[] = [
      "concatenated_string",
      "string_array",
      "chained_string",
    ];
    const nodeType: any = node.type;

    if (
      (specificTypes.indexOf(nodeType) === -1 &&
        nodeType.indexOf("string") !== -1) ||
      nodeType.indexOf("char") !== -1
    ) {
      tokens.push([
        node.children[0].startPosition,
        node.children[node.children.length - 1].endPosition,
      ]);
      types.push(nodeType);
      return;
    }
    for (const child of node.children) {
      this.get_tokens(child, tokens, types);
    }
  }

  private norm_untokenize(
    poses: any[],
    tokens: string[],
    types: string[],
    litnames: string[][],
    lits: { [key: string]: string[] },
    comment = "remove",
    indent = true
  ) {
    let codeString = "";
    let prevSp: any = null;
    let prevEp: any = null;
    let prevIndent = 0;
    let indentSize = -1;
    for (let i = 0; i < poses.length; i++) {
      const pos = poses[i];
      const token: string = tokens[i];
      const tp: string = types[i];
      if (tp === "new_line" || tp === "\n") {
        codeString += "\n";
        continue;
      }
      // const sp = pos[0]
      const keyRow: string = "row";
      const keyCol: string = "column";
      const keyRegex: string = "regex";
      const keyChar: string = "char";
      const keyStr: string = "str";
      const keyNum: string = "num";
      // const key
      const sp = [pos[0][keyRow], pos[0][keyCol]];
      const ep = [pos[1][keyRow], pos[1][keyCol]];
      let addToken: string = token;

      if (tp.indexOf("comment") !== -1) {
        if (comment === "normalize") {
          addToken = "#<COMMENT>";
        } else if (comment === "keep") {
          addToken = token;
        } else {
          addToken = token.startsWith(" ") ? " " : "";
        }
      } else if (
        litnames[3].indexOf(tp) !== -1 &&
        this.keywords.indexOf(token) === -1
      ) {
        if (lits[keyRegex].indexOf(token) !== -1) {
          if (this.specialTokensMap.hasOwnProperty(token)) {
            addToken = this.specialTokensMap[token];
          } else {
            addToken = "<REGEX_LIT:" + token + ">";
          }
        } else {
          addToken = "<REGEX_LIT>";
        }
      } else if (
        litnames[2].indexOf(tp) !== -1 &&
        this.keywords.indexOf(token) === -1
      ) {
        const charQuoteOptions = ["'", '"'];
        let startQuote: string = "";
        let endQuote: string = "";
        const qualifierRegex = /^[a-z]+/;
        const qualifierRegexGlobal = /^[a-z]+/g;
        const qualifierMatch = token.match(qualifierRegex);
        let qualifier = "";
        if (qualifierMatch != null) {
          qualifier = qualifierMatch[0];
        }
        const tokenString = token.replace(qualifierRegexGlobal, "");
        let charLit = tokenString;
        for (const q of charQuoteOptions) {
          if (tokenString.startsWith(q)) {
            startQuote = q;
            charLit = charLit.substring(q.length);
            if (tokenString.endsWith(q)) {
              endQuote = q;
              charLit = charLit.substring(0, charLit.length - q.length);
            }
            break;
          }
        }
        for (const sc in this.unicodeCharsMap) {
          if (this.unicodeCharsMap.hasOwnProperty(sc)) {
            const tc = this.unicodeCharsMap[sc];
            charLit = charLit.replace(sc, tc);
          }
        }
        if (this.specialTokensMap.hasOwnProperty(charLit)) {
          addToken = this.specialTokensMap[charLit];
        } else {
          if (lits[keyChar].indexOf(charLit) !== -1) {
            addToken =
              qualifier + startQuote + "<CHAR_LIT:" + charLit + ">" + endQuote;
          } else {
            addToken = qualifier + startQuote + "<CHAR_LIT>" + endQuote;
          }
        }
      } else if (litnames[1].includes(tp) && !this.keywords.includes(token)) {
        let strLit = "";
        let qualifier = "";

        const cppRawStringIdentifier = 'R"(';
        if (token.startsWith(cppRawStringIdentifier)) {
          // This is a C++ Raw String scenario. Must be handled separately.
          qualifier = "R";
          strLit = token
            .substring(cppRawStringIdentifier.length)
            .slice(0, -')"'.length);
        } else {
          const strQuoteOptions = ["'''", '"""', "'", '"', "`"];
          const qualifierRegex = /^([a-z]+|@)/;
          const qualifierRegexGlobal = /^([a-z]+|@)/g;
          const qualifierMatch = token.match(qualifierRegex);
          if (qualifierMatch != null) {
            qualifier = qualifierMatch[0];
          }
          const tokenString = token.replace(qualifierRegexGlobal, "");
          strLit = tokenString;
          for (const q of strQuoteOptions) {
            // const q = strQuoteOptions[index];
            if (tokenString.startsWith(q)) {
              strLit = strLit.substring(q.length);
              if (tokenString.endsWith(q)) {
                strLit = strLit.substring(0, strLit.length - q.length);
              }
              break;
            }
          }
        }
        for (const sc in this.unicodeCharsMap) {
          if (this.unicodeCharsMap.hasOwnProperty(sc)) {
            const tc = this.unicodeCharsMap[sc];
            strLit = strLit.replace(new RegExp(sc, "g"), tc);
          }
        }
        if (this.specialTokensMap.hasOwnProperty(strLit)) {
          addToken = this.specialTokensMap[strLit];
        } else {
          if (lits[keyStr].indexOf(strLit) !== -1) {
            addToken = qualifier + "<STR_LIT:" + strLit + ">";
          } else {
            addToken = qualifier + "<STR_LIT>";
          }
        }
      } else if (litnames[0].includes(tp) && !this.keywords.includes(token)) {
        if (lits[keyNum].indexOf(token) !== -1) {
          if (this.specialTokensMap.hasOwnProperty(token)) {
            addToken = this.specialTokensMap[token];
          } else {
            addToken = "<NUM_LIT:" + token + ">";
          }
        } else {
          addToken = "<NUM_LIT>";
        }
      }

      if (prevSp == null) {
        codeString += addToken;
      }

      if (prevEp != null) {
        if (sp[0] === prevEp[0] && sp[1] === prevEp[1]) {
          codeString += addToken;
        } else if (sp[0] === prevEp[0]) {
          if (codeString[codeString.length - 1] !== " ") {
            codeString += " ";
          }
          codeString += addToken;
        } else {
          if (indent && addToken) {
            codeString += "\n";
            let omit = false;
            if (sp[1] !== prevIndent && prevIndent === 0 && indentSize === -1) {
              indentSize = sp[1] - prevIndent;
            }
            if (sp[1] - prevIndent > 0) {
              if (sp[1] - prevIndent > 2 * indentSize) {
                omit = true;
              } else {
                for (let jj = prevIndent; jj < sp[1]; jj += indentSize) {
                  codeString += "<INDENT>";
                }
              }
            } else if (sp[1] - prevIndent < 0) {
              for (let kk = sp[1]; kk < prevIndent; kk = kk + indentSize) {
                codeString += "<DEDENT>";
              }
            }
            codeString += addToken;
            if (!omit) {
              prevIndent = sp[1];
            }
          } else {
            codeString += "\n";
            for (let ii = 0; ii < sp[1]; ii++) {
              codeString += " ";
            }
            codeString += addToken;
          }
        }
      }
      prevSp = sp;
      prevEp = ep;
    }
    const processedCode = codeString.replace(/^\s*/g, "");
    const ret = processedCode.replace(/\s*\n/g, "\n");
    return ret;
  }
}
