import { ICompletionResultCallback } from "../../resultCache/completionResultCallback";
import { ILoggingService } from "../logging";

export abstract class AbstractCompletionService {
  public constructor(protected Logger: ILoggingService) {}

  public abstract request(
    requestId: number,
    context: string,
    lang: string,
    completionState: ICompletionResultCallback,
    cancellationToken: { isCancellationRequested: boolean }
  ): Promise<RequestResult>;

  public abstract getFullName(): string;
}

/**
 * Different ways that a completion request can end.
 */
export enum RequestResult {
  /**
   * The request was successful.
   */
  Success,
  /**
   * The request was cancelled and exited early.
   */
  Cancelled,
  /**
   * The request took too long and timed out, exiting early.
   */
  TimedOut,
  /**
   * Something went wrong in an unexpected way that isn't covered by any of the other options.
   */
  Unknown,
}
