import p = require("proxyquire");
import { Node, Options } from "vis-network";
import { Position, Range } from "vscode";
import Parser = require("web-tree-sitter");
import { ParsedCode } from "../../services/normalization/normalizationService";
import {
  NodeTag,
  TaggedNodes as TaggedNodes,
} from "../../services/syntacticFilter/treeSitterSyntacticFilter";
import { GraphData } from "./graphData";

export function parsedCodeToGraphData(
  parsedCode: ParsedCode,
  tags: TaggedNodes,
  options: Options
): GraphData {
  let graphData: GraphData = {
    nodes: [],
    edges: [],
    options,
  };

  // Colors are applied from top to bottom
  let nodeTagColorMap: Map<NodeTag, string> = new Map<NodeTag, string>([
    [NodeTag.OverlapsSuggestion, "#eeeeee"],
    [NodeTag.InsideSuggestion, "#aaaaaa"],
    [NodeTag.Error, "#990000"],
    [NodeTag.ErrorChild, "#efb4b4"],
  ]);

  // Nodes to traverse
  const nodes: Parser.SyntaxNode[] = [parsedCode.tree.rootNode];
  while (nodes.length > 0) {
    const node = nodes.shift();
    if (node === undefined) {
      throw new Error("node was undefined but shouldn't be");
    }
    let label: string[] = [];

    label.push(
      `L${node.startPosition.row}:${node.startPosition.column} - L${node.endPosition.row}:${node.endPosition.column}`
    );
    label.push(`<i>${node.type}</i>`);
    if (node.hasError()) {
      label.push("<b>hasError</b>");
    }

    // quick and dirty escape html tags. otherwise they can break the layout of the graph
    // https://stackoverflow.com/a/18750001
    let mouseoverLabel = node.text.replace(
      /[\u00A0-\u9999<>\&]/g,
      function (i) {
        return "&#" + i.charCodeAt(0) + ";";
      }
    );

    let thisNodeTags = tags.getNodeTags(node.id);
    if (thisNodeTags.size > 0) {
      let tagNames: string[] = [];
      for (let tag of thisNodeTags.values()) {
        tagNames.push(NodeTag[tag]);
      }
      label.push(tagNames.join(","));
    }

    let newNode: Node = {
      id: node.id,
      label: label.join("\n"),
      title: mouseoverLabel,
    };

    // Colorize node based on its tags
    for (let [tag, tagColor] of nodeTagColorMap) {
      if (thisNodeTags.has(tag)) {
        newNode.color = tagColor;
      }
    }

    graphData.nodes.push(newNode);

    for (const child of node.children) {
      nodes.push(child);
      graphData.edges.push({
        from: node.id,
        to: child.id,
      });
    }
  }
  return graphData;
}

export const DEFAULT_GRAPH_OPTIONS: Options = {
  autoResize: true,
  height: "1200px",
  clickToUse: false,
  edges: {
    selectionWidth: 5,
  },
  layout: {
    improvedLayout: true,
    hierarchical: {
      direction: "UD",
      blockShifting: false,
      edgeMinimization: false,
      sortMethod: "directed",
      shakeTowards: "roots",
      nodeSpacing: 175,
      //levelSeparation: 400,
    },
  },
  interaction: {
    multiselect: true,
  },
  nodes: {
    color: {
      highlight: {
        border: "#FF0000",
      },
    },
    size: 15,
    font: {
      size: 20,
      color: "#000000",
      face: "sans-serif",
      background: "#FFFFFF00",
      align: "center",
      strokeColor: "#00000000",
      strokeWidth: 0,
      multi: "html",
    },
    shape: "box",
  },
  physics: false,
};
