// Entry ring 1: It is safe to assume we are running inside of VSCode and have access to VSCode api, but
// architecture is not assured; do not import any files which could result in a native dependency (onnxruntime)
// being imported, or unsupported architectures will be unable to fail gracefully.
import { IExtension } from "./extension";
import { StubToastExtensionFactory } from "./stubActivation";
import * as vscode from "vscode";
import * as constants from "../services/survey/index";
import {
  TelemetryEventNames,
  TelemetryPropertyNames,
} from "../@types/telemetry";

/**
 * Lazily instantiated instance of the extension.
 */
let extension: undefined | IExtension = undefined;

function activate(context: any) {
  if (extension === undefined) {
    // Conditions to test before we activate for real.

    // If vscode is using an unsupported architecture, we should avoid activating since otherwise
    // trying to load a missing native module will probably bring down the extension host.
    if (process.arch !== "x64" && process.arch !== "arm64") {
      extension = new StubToastExtensionFactory(
        `IntelliCode Completions is not currently supported on architecture ${process.arch}. x64 and arm64 are supported.`,
        constants.UNSUPPORTED_ARCH_NOTIFICATION_DO_NOT_SHOW_AGAIN,
        TelemetryEventNames.UNSUPPORTED_ARCH,
        {
          [TelemetryPropertyNames.PROCESS_ARCH_PLATFORM]: `${process.arch} ${process.platform}`,
        }
      ).Extension;
      return extension.activate(context);
    }

    // If copilot is present, we don't want to activate since our completions will conflict.
    const copilotEnabled =
      vscode.extensions.getExtension("github.copilot") !== undefined;
    if (copilotEnabled) {
      extension = new StubToastExtensionFactory(
        "IntelliCode will not show completions because Copilot is enabled.",
        constants.COPILOT_NOTIFICATION_DO_NOT_SHOW_AGAIN,
        TelemetryEventNames.COPILOT_ENABLED
      ).Extension; // returns stub activate
      return extension.activate(context);
    }

    // importing vscodeExtensionEntry is delayed until this point. If we were to import it too soon on an unsupported
    // architecture, the missing native onnxruntime module would cause activation to fail before we could handle that gracefully.
    extension =
      require("./extensionFactory").defaultExtensionFactory(context).Extension;
  }
  return extension?.activate(context);
}

function deactivate() {
  return extension?.deactivate();
}

export { activate, deactivate };
