import * as crypto from "crypto";
import { IExperimentationTelemetry } from "tas-client";
import TelemetryReporter from "vscode-extension-telemetry";
import { ITelemetrySender } from "./sender";
import { LogLevel } from "../logging";
import { Instance as Logger } from "../logging/VSCodeOutputLogger";
import * as vscode from "vscode";
import EventEmitter = require("events");
import {
  TelemetryEventEmitter,
  TelemetryEventNames,
  TelemetryMeasureNames,
  TelemetryPropertyNames,
} from "../../@types/telemetry";

export function initVsCodeTelemetryService(context: vscode.ExtensionContext) {
  /**
   * vscode.ExtensionContext according to the .d.ts file does not expose .extension
   * However, it exists and is usable.
   *
   * This may cause problems if vscode changes their API.
   */
  //@ts-ignore
  const { aiKey, name, version } = context.extension.packageJSON;
  return new Telemetry(name, version, aiKey);
}

function shortHashPII(value: string): string {
  // store a short hash if collecting PII is not allowed.
  const hash = crypto.createHash("sha256");
  hash.update(value);
  return hash.digest("hex").substr(0, 8);
}

function removePath(filePath: string): string {
  // Path names are PII, so we need to remove all parts preceding the filename
  return filePath.replace(/([A-Z]:)?[\\\/](.*[\\\/])*/gi, "");
}

export class Telemetry implements IExperimentationTelemetry, ITelemetrySender {
  public static readonly canCollectPII = false; // todo: make this configurable

  public reporter: TelemetryReporter;
  private contextProperties: Partial<Record<TelemetryPropertyNames, any>>;
  private correlationEvent: TelemetryEvent | undefined;

  /**
   * EventEmitter that will be lazily initialized.
   * Currently used during testing.
   */
  private eventEmitter?: TelemetryEventEmitter;

  constructor(name: string, version: string, aiKey: string) {
    this.reporter = new TelemetryReporter(name, version, aiKey);
    this.contextProperties = {};
  }

  /**
   * Initialize and return an event emitter
   */
  public get EventEmitter() {
    if (this.eventEmitter === undefined) {
      this.eventEmitter = new EventEmitter();
    }
    return this.eventEmitter;
  }

  public addContextProperty(
    property: TelemetryPropertyNames,
    value: any,
    isPII = false
  ): void {
    // no need to set `undefined` values
    if (value === undefined) {
      return;
    }

    const valueString = String(value);

    if (isPII && !Telemetry.canCollectPII) {
      this.contextProperties[property] = shortHashPII(valueString);
    } else {
      this.contextProperties[property] = valueString;
    }
  }

  public removeContextProperty(property: TelemetryPropertyNames): void {
    delete this.contextProperties[property];
  }

  public sendTelemetryEvent(
    eventName: TelemetryEventNames,
    properties?: Partial<Record<TelemetryPropertyNames, any>>,
    measures?: Partial<Record<TelemetryMeasureNames, number>>
  ) {
    if (this.reporter === undefined) {
      Logger.write(
        LogLevel.ERROR,
        "Tried to send a telemetry event but the reporter has been disposed."
      );
      return;
    }

    this.reporter.sendTelemetryEvent(
      eventName,
      this.addContextPropertiesToObject(properties),
      measures
    );
    this.eventEmitter?.emit(eventName, {
      properties: properties,
      measures: measures,
    });
    this.eventEmitter?.emit("telemetrySent", {
      telemetryEventName: eventName,
      properties: properties,
      measures: measures,
    });
  }

  public setSharedProperty(name: TelemetryPropertyNames, value: string): void {
    // This will allow us to add a context property to the telemetry events.
    // By doing this, we will be able to publish the assignment context we get from experimentation service.
    this.addContextProperty(name, value);
  }

  public postEvent(
    eventName: TelemetryEventNames,
    props: Map<string, string>
  ): void {
    // This allows us to post the event
    // whenever a flight is requested from the client.
    const obj: any = {};

    // Converting the Map into an object.
    for (const prop of props) {
      obj[prop[0]] = prop[1];
    }

    this.sendTelemetryEvent(eventName, obj);
  }

  public sendFault(
    eventName: TelemetryEventNames,
    type: FaultType,
    details?: string,
    exception?: any,
    correlatedEvent?: TelemetryEvent
  ) {
    new Fault(
      this,
      eventName,
      type,
      details,
      exception,
      correlatedEvent
    ).send();
  }

  public setCorrelationEvent(correlationEvent: TelemetryEvent) {
    this.correlationEvent = correlationEvent;
  }

  public removeCorrelationEvent(correlationEvent: TelemetryEvent) {
    if (this.correlationEvent === correlationEvent) {
      this.correlationEvent = undefined;
    }
  }

  public startTimedEvent(
    eventName: TelemetryEventNames,
    correlate = false
  ): TimedEvent {
    return new TimedEvent(this, eventName, correlate);
  }

  public correlate(telemetryEvent: TelemetryEvent): void {
    if (this.correlationEvent) {
      telemetryEvent.correlateWith(this.correlationEvent);
    }
  }

  private addContextPropertiesToObject(
    properties?: Partial<Record<TelemetryPropertyNames, any>>
  ) {
    return Object.assign({}, this.contextProperties, properties);
  }
}

export class TelemetryEvent {
  protected telemetryInstance: Telemetry;
  private eventName: TelemetryEventNames;
  private properties: Partial<Record<TelemetryPropertyNames, string>>;
  private measures: Partial<Record<TelemetryMeasureNames, number>>;
  private correlationId: number;

  constructor(
    telemetryInstance: Telemetry,
    eventName: TelemetryEventNames,
    correlate = false
  ) {
    this.telemetryInstance = telemetryInstance;
    this.eventName = eventName;
    this.properties = {};
    this.measures = {};
    // tslint:disable-next-line:insecure-random
    this.correlationId = Math.floor(Math.random() * Number.MAX_SAFE_INTEGER);
    if (correlate) {
      telemetryInstance.correlate(this);
    }
  }

  public addProperty(
    property: TelemetryPropertyNames,
    value: any,
    isPII = false
  ): void {
    // no need to set `undefined` values
    if (value === undefined) {
      return;
    }

    const valueString = String(value);

    if (isPII && !Telemetry.canCollectPII) {
      this.properties[property] = shortHashPII(valueString);
    } else {
      this.properties[property] = valueString;
    }
  }

  public propertyExists(property: string) {
    return property in this.properties;
  }

  public addMeasure(measure: TelemetryMeasureNames, value: number): void {
    this.measures[measure] = value;
  }

  public getCorrelationId(): number {
    return this.correlationId;
  }

  public correlateWith(otherEvent: TelemetryEvent) {
    this.correlationId = otherEvent.getCorrelationId();
  }

  public correlateWithId(correlationId: number) {
    this.correlationId = correlationId;
  }

  public async send() {
    this.addMeasure(TelemetryMeasureNames.CORRELATION_ID, this.correlationId);
    this.telemetryInstance.sendTelemetryEvent(
      this.eventName,
      this.properties,
      this.measures
    );
  }
}

export class Fault extends TelemetryEvent {
  constructor(
    telemetryInstance: Telemetry,
    eventName: TelemetryEventNames,
    type: FaultType,
    details?: string,
    exception?: any,
    correlatedEvent?: TelemetryEvent
  ) {
    super(telemetryInstance, eventName);
    this.addProperty(TelemetryPropertyNames.FAULT_TYPE, FaultType[type]);
    if (details) {
      this.addProperty(
        TelemetryPropertyNames.EVENT_MESSAGE,
        removePath(details)
      );
    }
    let exceptionStack = "";
    if (exception && exception.stack && typeof exception.stack === "string") {
      exceptionStack += removePath(exception.stack);
    }
    if (!exceptionStack) {
      exceptionStack = "No Stack";
    }
    this.addProperty(
      TelemetryPropertyNames.EVENT_EXCEPTION_STACK,
      exceptionStack
    );
    if (correlatedEvent) {
      this.correlateWith(correlatedEvent);
    }
  }
}

export class TimedEvent extends TelemetryEvent {
  private startTime: number;
  private lastMarkTime: number;
  constructor(
    telemetryInstance: Telemetry,
    eventName: TelemetryEventNames,
    correlate = false
  ) {
    super(telemetryInstance, eventName, correlate);
    this.startTime = new Date().getTime();
    this.lastMarkTime = this.startTime;
  }

  public markTime(markName: TelemetryMeasureNames, fromStart = false) {
    const currentTime = new Date().getTime();
    const duration = fromStart
      ? currentTime - this.startTime
      : currentTime - this.lastMarkTime;
    this.lastMarkTime = currentTime;
    this.addMeasure(markName, duration);
  }

  public end(result: TelemetryResult, message?: string, sendNow = true) {
    this.addProperty(
      TelemetryPropertyNames.EVENT_RESULT,
      TelemetryResult[result]
    );
    if (message) {
      this.addProperty(
        TelemetryPropertyNames.EVENT_MESSAGE,
        removePath(message)
      );
    }
    this.markTime(TelemetryMeasureNames.EVENT_DURATION, true);
    this.telemetryInstance.removeCorrelationEvent(this);
    if (sendNow) {
      this.send();
    }
  }
}

export enum FaultType {
  Error,
  User,
  Unknown,
}

export enum TelemetryResult {
  Cancel,
  Success,
  Failure,
  UserFailure,
  IndeterminateFailure,
}
