/**
 * Represents a range within a document, without taking a dependency on vscode types.
 */
export class DocumentRange {
  public constructor(
    public startLine: number,
    public startColumn: number,
    public endLine: number,
    public endColumn: number
  ) {}

  /**
   * Whether a location in the document is contained within this range.
   * @param line The location line to test.
   * @param column The location column to test.
   * @returns True if the location at line & column is within this range.
   */
  public containsLocation(line: number, column: number): boolean {
    const locationIsAfterStart =
      line > this.startLine ||
      (line == this.startLine && column >= this.startColumn);
    const locationIsBeforeEnd =
      line < this.endLine || (line == this.endLine && column <= this.endColumn); // viv: should it be <= this.endColumn??
    return locationIsAfterStart && locationIsBeforeEnd;
  }

  /**
   * Whether another range is contained completely within this one.
   * @param other The range to test.
   * @returns True if the other range is completely within this range.
   */
  public containsRange(other: DocumentRange): boolean {
    return (
      this.containsLocation(other.startLine, other.startColumn) &&
      this.containsLocation(other.endLine, other.endColumn)
    );
  }

  /**
   * Whether another range has any part which overlaps another range.
   * @param other The range to test.
   * @returns True if the other range overlaps any location of this one.
   */
  public rangesOverlap(other: DocumentRange): boolean {
    return (
      this.containsLocation(other.startLine, other.startColumn) || // Start of other lands within this range
      this.containsLocation(other.endLine, other.endColumn) || // End of other lands within this range
      other.containsRange(this)
    ); // The other range completely contains this one - that's the only other way they could overlap if the other's start and end points are outside
  }
}
