import * as vscode from "vscode";

/**
 * Helper function to calculate current context taking IntelliSense into consideration.
 * 1) w/ IntelliSense
 *   Code before IntelliSense + IntelliSense selection
 * 2) w/o IntelliSense
 *   Just the code before the current trigger
 * @param document Document to provide inline completions to
 * @param position Position where InlineCompletion has been triggered
 * @param context InlineCompletionContext passed in by vscode
 * @returns The context to be used taking IntelliSense into consideration
 */
export function getContext(
  document: vscode.TextDocument,
  position: vscode.Position,
  context: vscode.InlineCompletionContext
) {
  const codeBefore: string[] = [];
  // add previous cells
  if ('notebook' in document && (document as any).notebook !== undefined) {
    const notebook = (document as any).notebook;
    for (const cell of notebook.getCells()) {
      if (cell.document === document) {
        break;
      }
      codeBefore.push(cell.document.getText());
    }
  }
  codeBefore.push(context.selectedCompletionInfo !== undefined
    ? /**
       * IntelliSense selection active.
       * Code before IntelliSense + IntelliSense selection
       */
      getCodeBefore(context.selectedCompletionInfo.range.start, document) +
        context.selectedCompletionInfo.text
    : /**
       * No IntelliSense.
       * Just the code before the cursor.
       */
      getCodeBefore(position, document));
  
  // Avoid calling join if there's only one element, which we expect to be true
  // in all documents except notebooks.
  if (codeBefore.length === 1){
    return codeBefore[0];
  }
  return codeBefore.join("\n");
}

export function getCodeBefore(
  position: vscode.Position,
  document: vscode.TextDocument
) {
  return document.getText(new vscode.Range(document.positionAt(0), position));
}

export function getCodeAfter(
  position: vscode.Position,
  document: vscode.TextDocument,
  includeLineBreak = false
) {
  const afterEnd = includeLineBreak
    ? getAfterEOLPosition(position, document)
    : getCurrentEOLPosition(position, document);
  return document.getText(new vscode.Range(position, afterEnd));
}

function getCurrentEOLPosition(
  position: vscode.Position,
  document: vscode.TextDocument
) {
  return document.lineAt(position).range.end;
}

function getAfterEOLPosition(
  position: vscode.Position,
  document: vscode.TextDocument
) {
  return document.lineAt(position).rangeIncludingLineBreak.end;
}

export function showCompletion(
  activeEditor: vscode.TextEditor,
  decorationType: vscode.TextEditorDecorationType,
  completionToShow: string,
  positionToShow: vscode.Position
) {
  activeEditor.setDecorations(decorationType, [
    decorationOption(positionToShow, completionToShow),
  ]);
}

function decorationOption(position: vscode.Position, suggestion: string) {
  return {
    range: new vscode.Range(position, position),
    renderOptions: {
      before: {
        contentText: suggestion,
      },
      dark: {
        before: {
          color: "rgba(196, 196, 196, 0.4)",
        },
      },
      light: {
        before: {
          color: "rgba(0, 0, 0, 0.4)",
        },
      },
    },
  };
}

/**
 * Sometimes our model returns the exact same line as before.
 * This function checks if that's the case.
 *
 * @param fullLineWithCompletion What the current line would look like w/ the completion
 * @param document The VSCode document object to check
 * @param position The VSCode position object. Will start checking from the line before this.
 * @returns Whether the first non-whitespace line before position is equal to the completion
 */
export function duplicateLineExistsBefore(
  fullLineWithCompletion: string,
  document: vscode.TextDocument,
  position: vscode.Position
) {
  const trimmedFullLine = fullLineWithCompletion.trim();
  for (let idx = position.line - 1; idx >= 0; idx--) {
    const curLine = document.lineAt(idx);
    if (curLine.isEmptyOrWhitespace) {
      continue;
    }
    return curLine.text.trim() === trimmedFullLine;
  }
  return false;
}

/**
 * Sometimes our model returns the exact same line as before.
 * This function checks if that's the case.
 *
 * @param fullLineWithCompletion The completion string to be shown
 * @param document The VSCode document object to check
 * @param position The VSCode position object. Will start checking from the line after this.
 * @returns Whether the first non-whitespace line after position is equal to the completion
 */
export function duplicateLineExistsAfter(
  fullLineWithCompletion: string,
  document: vscode.TextDocument,
  position: vscode.Position
) {
  const trimmedFullLine = fullLineWithCompletion.trim();
  for (let idx = position.line + 1; idx < document.lineCount; idx++) {
    const curLine = document.lineAt(idx);
    if (curLine.isEmptyOrWhitespace) {
      continue;
    }
    return curLine.text.trim() === trimmedFullLine;
  }

  return false;
}
