import FeedbackSurveyService from "./feedbackSurveyService";

// Survey response options
export const NOTIFICATION_RESPONSE_YES: string = "Yes";
export const NOTIFICATION_RESPONSE_MAYBE_LATER: string = "Maybe later";
export const NOTIFICATION_RESPONSE_DO_NOT_SHOW_AGAIN: string =
  "Do not show again";

const EXTENSION_ID_PREFIX =
  "visualstudioexptteam.vscodeintellicode-completions/";

// Global State Keys
export const DO_NOT_SHOW_AGAIN_SELECTED: string =
  EXTENSION_ID_PREFIX + "doNotShowAgain";
export const NUMBER_OF_COMPLETIONS_SHOWN: string =
  EXTENSION_ID_PREFIX + "numberOfCompletionsShown";
export const NOTIFICATION_ALREADY_SHOWN: string =
  EXTENSION_ID_PREFIX + "notificationAlreadyShown";
export const SHOW_NOTIFICATION_AT_TIME =
  EXTENSION_ID_PREFIX + "showNotificationAtTime";
export const LAST_TIME_USER_WAS_ACTIVE: string =
  EXTENSION_ID_PREFIX + "lastTimeUserWasActive";
export const COPILOT_NOTIFICATION_DO_NOT_SHOW_AGAIN: string =
  EXTENSION_ID_PREFIX + "copilotNotificationDoNotShowAgain";
export const UNSUPPORTED_ARCH_NOTIFICATION_DO_NOT_SHOW_AGAIN: string =
  EXTENSION_ID_PREFIX + "unsupportedArchNotificationDoNotShowAgain";
export const COPILOT_ENABLED: string = EXTENSION_ID_PREFIX + "copilotEnabled";

// Survey constants
export const FEEDBACK_SURVEY_URI: string =
  "https://aka.ms/ICVSCodeInlineSurvey";
export const FEEDBACK_MESSAGE: string =
  "Would you like to provide feedback for IntelliCode whole line completions?";

/**
 * When user clicks on Maybe Later, they'll see the notification again in 24 hours
 */
export const MAYBE_LATER_WAIT_TIME: number = 1000 * 60 * 60 * 24; // 24 hours
/**
 * The user gets a feedback notification only after they've seen 500 IntelliCode completions
 */
export const CAN_SHOW_SURVEY_AFTER_NUMBER_OF_COMPLETIONS: number = 500;
/**
 * The user needs to be in rest state i.e. inactive for 15 seconds before they get a notification
 */
export const USER_IN_REST_STATE_TIME: number = 1000 * 15; // 15 seconds

export { FeedbackSurveyService };
