import TypedEmitter from "typed-emitter";

export enum TelemetryEventNames {
  EXTENSION_ACTIVATED = "wlc/extension-activated",
  COMPLETION_PROVIDER_CALLED = "wlc/completion-provider-called",
  COMPLETION_PROVIDER_CANCEL_REQUESTED = "wlc/completion-provider-cancel-requested",
  COMPLETION_SHOWN = "wlc/completion-shown",
  COMPLETION_ACCEPTED = "wlc/completion-accepted",
  COMPLETION_REJECTED = "wlc/completion-rejected",
  COMPLETION_REQUEST = "wlc/completion-request",
  CACHE_STORE = "wlc/cache-store",
  CACHE_ACCESS = "wlc/cache-access",
  UNEXPECTED_EXCEPTION = "wlc/unexpected-exception",
  EXTENSION_ACTIVATION_FAULT = "wlc/extension-activation-fault",
  COPILOT_ENABLED = "wlc/copilot-enabled",
  UNSUPPORTED_ARCH = "wlc/unsupported-arch",
  EMPTY_EXTENSION_ACTIVATION_FAULT = "wlc/empty-extension-activation-fault",
  COPILOT_DISABLED_AFTER_BEING_ENABLED = "wlc/copilot-disabled-after-being-enabled",
}

export enum TelemetryPropertyNames {
  EVENT_RESULT = "wlc.Result",
  EVENT_MESSAGE = "wlc.Message",
  EVENT_EXCEPTION_STACK = "wlc.ExceptionStack",
  FAULT_TYPE = "wlc.FaultType",
  IS_INTERNAL = "vscode.isinternal",
  CACHE_HIT = "wlc.CacheHit",
  LANGUAGE_ID = "wlc.LanguageId",
  PROCESS_ARCH_PLATFORM = "wlc.process.archPlatform",
}

export enum TelemetryMeasureNames {
  CORRELATION_ID = "wlc.CorrelationId",
  EVENT_DURATION = "wlc.Duration",
  TIME_SINCE_LAST_PROVIDE = "wlc.TimeSinceLastProvide",
  TIME_TIL_CANCEL_REQ = "wlc.TimeTilCancelReq",
  NUM_REC = "wlc.NumberOfRecommendations",
  SEARCH_DEPTH = "wlc.SearchDepth",
  COMPLETION_LENGTH = "wlc.CompletionLength",
  CONFIDENCE_THRESHOLD = "wlc.ConfidenceThreshold",
  FIRST_TOKEN_SCORE = "wlc.FirstTokenScore",
  CUMULATIVE_LAST_TOKEN_SCORE = "wlc.CumulativeLastTokenScore",
  LENGTH_PENALIZED_LAST_TOKEN_SCORE = "wlc.LengthPenalizedLastTokenScore",
  TOKEN_COUNT = "wlc.TokenCount",
  SNIPPET_PLACEHOLDER_COUNT = "wlc.SnippetPlaceholderCount",
}

type TelemetryEvent = {
  /**
   * Telemetry specific event emitter.
   * Users will attach on to this event by listening for specific telemetry event names.
   * Hence only properties and measures will be passed as a parameter to the attached onEvent functions.
   *
   * Refer to below for notation:
   * https://www.typescriptlang.org/docs/handbook/release-notes/typescript-4-1.html#template-literal-types
   */
  [eventName in `${TelemetryEventNames}`]: (telemetryBody: {
    properties?: {
      [propertyName in `${TelemetryPropertyNames}`]?: string;
    };
    measures?: {
      [measureName in `${TelemetryMeasureNames}`]?: number;
    };
  }) => void;
} & {
  /**
   * This event is to be emitted whenever any telemetry is sent.
   * Hence it'll pass in the telemetry event name as a parameter to the attached onEvent functions.
   */
  telemetrySent: (telemetryBody: {
    telemetryEventName: string;
    properties?: {
      [propertyName in `${TelemetryPropertyNames}`]?: string;
    };
    measures?: {
      [measureName in `${TelemetryMeasureNames}`]?: number;
    };
  }) => void;
};

export type TelemetryEventEmitter = TypedEmitter<TelemetryEvent>;
