import { ILoggingService } from "../logging";
import { LanguageId } from "../../utils/language";
import * as Parser from "web-tree-sitter";
import { ParsedCode } from "../normalization/normalizationService";
import { DocumentRange } from "../../utils/range/documentRange";
import { matchBraces } from "../../utils/postProcRules";

export class TreeSitterSyntacticFilterService {
  /**
   *
   * @param Logger Instance of a LoggingService
   */
  public constructor(private Logger: ILoggingService) {}

  /**
   * Walks a syntax tree and applies tags to nodes.
   * @param parsedCode Parsed syntax tree to apply tags to.
   * @param previousParse Previous parsed syntax tree to use as reference for some tags (such as Changed)
   * @param insertedSuggestionRange A document range where a suggestion was applied, used as reference for some tags.
   * @returns Tagged nodes.
   */
  public tagSyntaxNodes(
    parsedCode: ParsedCode,
    previousParse: ParsedCode | undefined,
    insertedSuggestionRange: DocumentRange | undefined
  ): TaggedNodes {
    const tags: TaggedNodes = new TaggedNodes(parsedCode.lang);

    // If there was a previous parse to diff against, get the changed ranges.
    let changedRanges: Parser.Range[] | undefined;
    if (previousParse !== undefined) {
      changedRanges = parsedCode.tree.getChangedRanges(previousParse.tree);
    }

    // Walk the tree and collect interesting nodes.
    const nodes: {
      node: Parser.SyntaxNode;
      parentTags: Set<NodeTag>;
    }[] = [
      {
        node: parsedCode.tree.rootNode,
        parentTags: new Set<NodeTag>(),
      },
    ];

    while (nodes.length > 0) {
      let shouldVisitChildNodes = false; // so we know whether to visit child nodes
      const current = nodes.shift();
      if (current === undefined) {
        throw new Error("node was undefined but shouldn't be");
      }

      // hasError and isMissing are true if this node or any child nodes are errors or missing. Use this to direct
      // which child nodes we need to visit.
      if (current.node.hasError() || current.node.isMissing()) {
        shouldVisitChildNodes = true;
      }

      // Collect nodes that intersect with the suggestion.
      let nodeRange = new DocumentRange(
        current.node.startPosition.row,
        current.node.startPosition.column,
        current.node.endPosition.row,
        current.node.endPosition.column
      );
      if (insertedSuggestionRange !== undefined) {
        if (insertedSuggestionRange.rangesOverlap(nodeRange)) {
          tags.tagNode(current.node, NodeTag.OverlapsSuggestion);
          shouldVisitChildNodes = true;
        }
        if (insertedSuggestionRange.containsRange(nodeRange)) {
          tags.tagNode(current.node, NodeTag.InsideSuggestion);
          shouldVisitChildNodes = true;
        }
      }

      // Tags based on node type
      if (current.node.type === "ERROR" || current.node.type === "MISSING") {
        const nodeTags = this.getTagsForErrorAndMissingNodes(current.node);
        for (const tag of nodeTags) {
          tags.tagNode(current.node, tag);
        }
      } else if (current.node.type === "parenthesized_expression") {
        tags.tagNode(current.node, NodeTag.Parenthesized);
      } else if (parsedCode.lang === "python" && current.node.type === "print_statement") {
        // Python 2.x-style print statements should not be suggested.
        tags.tagNode(current.node, NodeTag.Error);
      }

      // If comparing two parses, tag nodes which are intersecting the parse change ranges
      if (changedRanges !== undefined) {
        for (const changedRange of changedRanges) {
          // If the error is completely before or completely after the changed range, it does not intersect the change
          if (
            (current.node.startIndex <= changedRange.startIndex &&
              current.node.endIndex <= changedRange.startIndex) ||
            (current.node.startIndex >= changedRange.endIndex &&
              current.node.endIndex >= changedRange.endIndex)
          ) {
            continue; // keep looping because another changed range may intersect with the error
          }
          tags.tagNode(current.node, NodeTag.Changed);
        }
      }

      if (current.node.children.length == 0) {
        tags.tagNode(current.node, NodeTag.Leaf);
      }

      // tags based on the parent's tags
      if (
        current.parentTags.has(NodeTag.Error) ||
        current.parentTags.has(NodeTag.ErrorChild)
      ) {
        tags.tagNode(current.node, NodeTag.ErrorChild);
      }

      if (current.parentTags.has(NodeTag.Parenthesized)) {
        tags.tagNode(current.node, NodeTag.Parenthesized);
      }

      // Optimization so we don't need to visit every node:
      // If the current node contains any ERROR or MISSING nodes, or intersects with the suggestion, we should visit all child nodes.
      // Revisit if we need to tag more nodes
      if (shouldVisitChildNodes) {
        const thisNodeTags = tags.getNodeTags(current.node.id);
        for (const child of current.node.children) {
          nodes.push({
            node: child,
            parentTags: thisNodeTags,
          });
        }
      }
    }
    return tags;
  }

  private getTagsForErrorAndMissingNodes(node: Parser.SyntaxNode): NodeTag[] {
    // Nodes with type "ERROR" and "MISSING" will be given the "Error" tag by default, with exceptions.
    // If a node is tagged both "Error" and "OverlapsSuggestion" then the whole prediction is filtered.
    // We want this usually to catch obviously incorrect code, but there are cases that cause parse errors which are actually not reason to discard the prediction.

    // If the node has unmatched braces, then allow the error
    const unmatchedBraces = matchBraces(node.text);
    if (unmatchedBraces.length > 0) {
      return [NodeTag.ErrorUnmatched];
    }

    if (node.id == node.tree.rootNode.id) {
      // Do *not* tag the root node if it's an error. These are likely to be false positives.
      return [];
    }

    // Any error/missing node which didn't match any of the exceptional cases.
    return [NodeTag.Error];
  }
}

// Tags that can be assigned to a node
export enum NodeTag {
  OverlapsSuggestion, // This node overlaps the gray text region
  InsideSuggestion, // This node is completely within the gray text region
  Error, // This is an ERROR or MISSING node
  ErrorChild, // This node is the child of an ERROR or MISSING node
  Changed, // This node was affected somehow by the suggestion (usually this is the entire document following the suggestion)
  Leaf, // This node has no children.
  Parenthesized, // This node is within a parenthesized expression (or is itself parentheses). TODO: Not used yet, revisit later. I was trying to allow parse errors which open a ( but don't close it
  ErrorUnmatched, // This node is an ERROR or MISSING node containing unmatched braces.
}

export class TaggedNodes {
  public tagNodeMap: Map<NodeTag, Map<number, Parser.SyntaxNode>>;
  constructor(public language: LanguageId) {
    this.tagNodeMap = new Map();
  }

  /**
   * Apply a tag to a syntax node.
   * @param node The node to tag. Node ids must stay consistent for tags to be valid.
   * @param tag The tag to apply to the node.
   */
  public tagNode(node: Parser.SyntaxNode, tag: NodeTag) {
    let idMapForTag = this.tagNodeMap.get(tag);

    if (idMapForTag === undefined) {
      idMapForTag = new Map();
      this.tagNodeMap.set(tag, idMapForTag);
    }

    idMapForTag.set(node.id, node);
  }

  /**
   * Get all nodes that are tagged with /all/ of the specified tags.
   * @param tags The set of tags to require on a node for it to be returned.
   * @returns The nodes which had all of the tags.
   */
  public getNodesWithAllTags(...tags: NodeTag[]): Parser.SyntaxNode[] {
    let collectedNodes: Parser.SyntaxNode[] = [];

    for (let i = 0; i < tags.length; i++) {
      let idMapForTag = this.tagNodeMap.get(tags[i]);

      if (idMapForTag === undefined) {
        // A tag was not present in the map, so we can conclude that there are no nodes that are in all the specified tags and exit early.
        return [];
      }

      if (i == 0) {
        // For the first tag, just copy the entire list
        collectedNodes = [...idMapForTag.values()];
      } else {
        // For subsequent tags, narrow down the set by pruning nodes that aren't in the map
        // Iterate backwards through the list of collected nodes so that the indices of untested nodes don't change when we delete them.
        for (let i = collectedNodes.length - 1; i >= 0; i--) {
          if (!idMapForTag.has(collectedNodes[i].id)) {
            collectedNodes.splice(i, 1);
          }
        }
      }
    }
    return collectedNodes;
  }

  /**
   * Get all tags that have been assigned to a node
   * @param nodeId The id of the node to get tags for.
   * @returns A set of tags that were assigned to the node.
   */
  public getNodeTags(nodeId: number): Set<NodeTag> {
    const nodeTags: Set<NodeTag> = new Set<NodeTag>();
    for (let [tag, idMapForTag] of this.tagNodeMap) {
      if (idMapForTag.has(nodeId)) {
        nodeTags.add(tag);
      }
    }
    return nodeTags;
  }
}
