// Entry ring 0: Do not import anything VSCode specific or architecture specific in this file.
import { env } from "process";
import { isMainThread } from "worker_threads";

/**
 * Filename of the entry point.
 * In a webpack bundle, this is the bundle's filename.
 * In tests, this should be entry.js.
 */
export const ENTRY_FILENAME = __filename;

/*
This extension's bundle switches between two entry points.
1. A VSCode extension, lazily created when activate() is called by VSCode
2. A worker thread, which the extension may instantiate.
*/

let vscodeExtensionEntry: any = undefined;

function activate(context: any) {
  if (vscodeExtensionEntry === undefined) {
    // the use of require() loads the vscode-specific file as late as possible
    vscodeExtensionEntry = require("./vscodeExtension/vscodeExtensionEntry");
  }
  return vscodeExtensionEntry.activate(context);
}

function deactivate() {
  if (vscodeExtensionEntry !== undefined) {
    return vscodeExtensionEntry.deactivate();
  }
}

export { activate, deactivate };

// If the worker thread isn't launching,
// build the bundle, and run it with node while having the below env variable set
// to find issues with unavailable imports (such as `vscode`)
if (!isMainThread || env.FORCE_WORKER_THREAD_ENTRY) {
  import("./workerThread/inferenceWorker").then((w) => {
    w.main();
  });
}
