import { Tensor } from "onnxruntime-node";

export const filledTensor = (
  shape: number[],
  type: Tensor.Type,
  fill: any = undefined
) => {
  const total = shape.reduce((prev, curr) => {
    return prev * curr;
  }, 1);
  const data = Array(total);
  if (fill) {
    data.fill(fill);
  }
  return new Tensor(type, data, shape);
};

// type NumericElementTypes = "int64" | "float32" | "uint16";
export function concat2d(a: Tensor, b: Tensor, dim: 0 | 1): any {
  if (dim === 0) {
    const outputArray: any[] = Array(...a.data, ...b.data);
    return new Tensor(a.type, outputArray, [a.dims[0] + b.dims[0], a.dims[1]]);
  } else {
    const outputArray: any[] = [];
    const lastDimSizeA = a.dims[a.dims.length - 1];
    const lastDimSizeB = b.dims[b.dims.length - 1];

    for (var i = 0; i < a.dims[0]; i++) {
      outputArray.push(
        ...a.data.slice(i * lastDimSizeA, i * lastDimSizeA + lastDimSizeA),
        ...b.data.slice(i * lastDimSizeB, i * lastDimSizeB + lastDimSizeB)
      );
    }
    return new Tensor(a.type, outputArray, [a.dims[0], a.dims[1] + b.dims[1]]);
  }
}
