import { MessagePort } from "worker_threads";
import { ITelemetrySender } from "../../services/telemetry/sender";
import { TelemetryMessage } from "../messages";

/**
 * Remote shim which implements ITelemetrySender by sending messages
 * to the extension host which will then send the telemetry.
 */
export class WorkerThreadTelemetryService implements ITelemetrySender {
  constructor(private port: MessagePort) {}
  sendTelemetryEvent(
    eventName: string,
    properties?: { [key: string]: string },
    measures?: { [key: string]: number }
  ): void {
    const telemetryMessage: TelemetryMessage = {
      kind: "TelemetryMessage",
      eventName: eventName,
      properties: properties,
      measures: measures,
    };
    this.port.postMessage(telemetryMessage);
  }
}
