import * as vscode from "vscode";
import { ILoggingService, LogLevel } from "../../services/logging";
import { INormalizationService } from "../../services/normalization";
import { ParsedCode } from "../../services/normalization/normalizationService";
import { NodeTag, TreeSitterSyntacticFilterService } from "../../services/syntacticFilter/treeSitterSyntacticFilter";
import { FromVsCodeRange } from "../range/conversions";
import {
  duplicateLineExistsAfter,
  duplicateLineExistsBefore,
} from "../vscodeEditor";

/**
 * Check that accepting our suggestion doesn't create a duplicate line
 * immediately above or below our current line.
 * @param fullLineWithCompletion What the full line would look like after our suggestion is accepted
 * @param document The document we are checking for duplicate lines
 * @param position Our position within the document
 * @param logger (Optional) Logger to output logs to
 * @param logPrefix (Optional) LogPrefix to attach to logs
 * @returns Boolean on whether we are not duplicating code immediately above or below
 */
export function isUniqueLine(
  fullLineWithCompletion: string,
  document: vscode.TextDocument,
  position: vscode.Position,
  logger?: ILoggingService,
  logPrefix?: string
): boolean {
  logPrefix = logPrefix ? logPrefix + " " : "";
  if (fullLineWithCompletion == "") {
    logger?.write(LogLevel.INFO, `${logPrefix}No completion available`);
    return false;
  }

  // We don't want the completed line to be a duplicate of another line above or below
  if (
    duplicateLineExistsBefore(fullLineWithCompletion, document, position) ||
    duplicateLineExistsAfter(fullLineWithCompletion, document, position)
  ) {
    logger?.write(
      LogLevel.INFO,
      `${logPrefix}` +
        `Skipping completion as it is a repeated above or below current line:\n` +
        `${fullLineWithCompletion}`
    );
    return false;
  }
  return true;
}

/**
 * Uses a syntax aware parser to parse the code to search for syntax errors.
 * Returns whether the code is syntactically correct.
 * 
 * Be aware that this function will return true if the parser fails to parse the code.
 * @param fullLineWithCompletion 
 * @param document 
 * @param position 
 * @param parsedCode 
 * @param codeParsingService 
 * @param logger 
 * @param logPrefix 
 * @returns 
 */

export function createDocumentWithCompletionApplied(document: vscode.TextDocument, position: vscode.Position, fullLineWithCompletion: string): string {
    const contextBeforeCurrentLine =
      position.line == 0
        ? ""
        : document.getText(
            new vscode.Range(
              new vscode.Position(0, 0),
              document.lineAt(position.line - 1).range.end
            )
          );

    const contextAfterCurrentLine =
      position.line == document.lineCount - 1
        ? ""
        : document.getText(
            new vscode.Range(
              document.lineAt(position.line + 1).range.start,
              document.lineAt(document.lineCount - 1).range.end
            )
          );

    return contextBeforeCurrentLine + "\n" + fullLineWithCompletion + "\n" + contextAfterCurrentLine;
}

export function isSyntacticallyCorrect(
  fullLineWithCompletion: string,
  postAcceptanceRange: vscode.Range,
  document: vscode.TextDocument,
  position: vscode.Position,
  parsedCode: ParsedCode,
  codeParsingService: INormalizationService,
  syntacticFilter: TreeSitterSyntacticFilterService,
  logger?: ILoggingService,
  logPrefix?: string
) : boolean {
  logPrefix = logPrefix ? logPrefix + " " : "";
  try {
    const resultingDocument = createDocumentWithCompletionApplied(document, position, fullLineWithCompletion);
    // todo: derive this from the existing parse instead of re-parsing
    const parsedWithCompletion = codeParsingService.parse(
      resultingDocument,
      document.languageId
    );
    let tags = syntacticFilter.tagSyntaxNodes(parsedWithCompletion, parsedCode, FromVsCodeRange(postAcceptanceRange));

    // todo: refactor repetition

    // If there are any error nodes in the resulting document that overlap the suggestion, reject the prediction
    {
      // this tagging might be incorrect, revisit
      const nodes = tags.getNodesWithAllTags(NodeTag.OverlapsSuggestion, NodeTag.Error);
      if (nodes.length > 0) {
        const printableNodes = nodes.map(n => `L${n.startPosition.row}:${n.startPosition.column}-L${n.endPosition.row}:${n.endPosition.column}`).join(", ");
        logger?.write(
          LogLevel.INFO,
          logPrefix + `Filtering completion because the suggestion overlaps a parse error ${printableNodes}`
        );
        for (const n of nodes){
          logger?.write(
            LogLevel.INFO,
            logPrefix + `node`
          );
        }
        return false;
      }
    }

    // If there are any comment nodes overlapping the suggestion range, reject the prediction.
    {
      const nodes = tags.getNodesWithAllTags(NodeTag.OverlapsSuggestion, NodeTag.Leaf);
      if (nodes.filter(n => n.type === "comment").length > 0){
        const printableNodes = nodes.map(n => `L${n.startPosition.row}:${n.startPosition.column}-L${n.endPosition.row}:${n.endPosition.column}`).join(", ");
        logger?.write(
          LogLevel.INFO,
          logPrefix + `Filtering completion because the suggestion overlaps a comment ${printableNodes}`
        );
        return false;
      }
    }
  } catch (e) {
    if (e instanceof Error) {
      logger?.write(
        LogLevel.ERROR,
        `error parsing the prediction: ${e.message}`
      );
    }
  }
  return true;
}
