import {
  TelemetryEventNames,
  TelemetryPropertyNames,
} from "../@types/telemetry";
import { LogLevel } from "../services/logging";
import * as vscode from "vscode";
import * as constants from "../services/survey/index";
import { Telemetry } from "../services/telemetry/vscodeTelemetry";
import { ExtensionApi } from "../extensionApi";
import { IExtension } from "./extension";
import { initVsCodeTelemetryService } from "../services/telemetry/vscodeTelemetry";
import { VSCodeOutputLogger } from "../services/logging/VSCodeOutputLogger";

/**
 * A simple extension activation path which only shows a warning toast and sends a telemetry event.
 * Use this when the extension should be prevented from initializing normally for some reason.
 */
export class StubToastExtensionFactory {
  private telemetry: Telemetry | undefined;

  /**
   * @param message Message to display to the user in a warning notification
   * @param doNotShowStateKey global state key to use to record if 'do not show again' is clicked
   * @param stubActivationTelemetryEvent the telemetry event to send if activation has been stubbed.
   * @param stubActivationTelemetryProperties telemetry properties to attach
   */
  constructor(
    private message: string,
    private doNotShowStateKey: string,
    private stubActivationTelemetryEvent: TelemetryEventNames,
    private stubActivationTelemetryProperties?: Partial<
      Record<TelemetryPropertyNames, any>
    >
  ) {}

  /**
   * Returns an object containing the activate and deactivate functions.
   * Exporting those two are the minimum requirements of a VSCode extension.
   */
  public get Extension(): IExtension {
    const ext = {
      activate: this.createStubActivate(),
      deactivate: () => {
        this.telemetry?.reporter.dispose();
      },
    };
    return ext;
  }

  public createStubActivate() {
    VSCodeOutputLogger.Instance.write(LogLevel.WARN, this.message);
    const activate = async (context: vscode.ExtensionContext) => {
      context.globalState.setKeysForSync([this.doNotShowStateKey]);
      if (this.telemetry === undefined) {
        this.telemetry = initVsCodeTelemetryService(context);
      }
      this.telemetry.sendTelemetryEvent(
        this.stubActivationTelemetryEvent,
        this.stubActivationTelemetryProperties
      );
      if (!context.globalState.get(this.doNotShowStateKey)) {
        const selection = await vscode.window.showWarningMessage(
          this.message,
          constants.NOTIFICATION_RESPONSE_DO_NOT_SHOW_AGAIN
        );

        if (selection === constants.NOTIFICATION_RESPONSE_DO_NOT_SHOW_AGAIN) {
          context.globalState.update(this.doNotShowStateKey, true);
        }
      }
      const api: ExtensionApi = {
        registerDevCommands: () => {},
        getTelemetryEmitter:
          this.telemetry === undefined
            ? () => {
                throw new Error("Telemetry Service not initialized");
              }
            : () => this.telemetry!.EventEmitter,
      };
      return api;
    };
    return activate;
  }
}
