import { MessagePort } from "worker_threads";
import { SettingsStorage } from "../../services/settings/settings";
import { SettingsRequestMessage, SettingsResponseMessage } from "../messages";

/**
 * Remote shim which implements settings storage by asking the extension for
 * the current value of settings, and returning the default if it is not already known.
 */
export class WorkerThreadSettingsStorage implements SettingsStorage {
  private cachedSettings: Map<string, any> = new Map<string, any>();

  constructor(private port: MessagePort) {}
  public get<T>(section: string, key: string, defaultValue: T): T {
    // Try to get the setting from the cache
    const cachedValue = this.cachedSettings.get(this.cacheKey(section, key));
    if (cachedValue !== undefined) {
      // We have a cached value, so just return it.
      return cachedValue;
    } else {
      // If it's missing, request the updated value from the main process, and return default
      this.requestUpdate(section, key, defaultValue);
      return defaultValue;
    }
  }

  public requestUpdate(section: string, key: string, defaultValue: any) {
    const requestMessage: SettingsRequestMessage = {
      kind: "SettingsRequestMessage",
      section: section,
      key: key,
      default: defaultValue,
    };
    this.port.postMessage(requestMessage);
  }

  public handleUpdateResponse(response: SettingsResponseMessage) {
    this.cachedSettings.set(
      this.cacheKey(response.section, response.key),
      response.value
    );
  }

  private cacheKey(section: string, key: string): string {
    return `${section}.${key}`;
  }
}
