// Entry ring 2: It is safe to reference VSCode APIs and reference native modules, VSCode is running on an architecture
// that is supported by onnxruntime.
import * as vscode from "vscode";
import { CompletionService } from "../services/completion";
import { CompletionState } from "../resultCache/completionState";
import { ExtensionApi } from "../extensionApi";
import { IntelliCodeInlineCompletionItemProvider } from "./inlineCompletionProvider";
import { ILoggingService, LogLevel } from "../services/logging";
import { INormalizationService } from "../services/normalization";
import {
  FeedbackSurveyService,
  USER_IN_REST_STATE_TIME,
} from "../services/survey";
import { isInternal } from "../utils/common";
import {
  Telemetry,
  TelemetryResult,
  FaultType,
} from "../services/telemetry/vscodeTelemetry";
import Settings from "../services/settings/settings";
import { VSCodeSettings } from "../services/settings/vscodeSettings";
import { ITelemetrySender } from "../services/telemetry/sender";
import {
  TelemetryEventNames,
  TelemetryPropertyNames,
} from "../@types/telemetry";
import postAcceptanceCommand from "./commands/postAcceptanceCommand";
import {
  registerCompletionVisualizationCommand,
  registerParseTreeVisualizationCommand,
} from "./commands/createVisualizationCommands";
import { TreeSitterSyntacticFilterService } from "../services/syntacticFilter/treeSitterSyntacticFilter";
import * as constants from "../services/survey/index";
import { createLineCompletionServiceWithRetriesAsync } from "../services/completion/localLineCompletionService"; // MARKER: might have to move this deeper idk
import { initWorkerThreadCompletionService } from "../services/completion/workerThreadLineCompletionService";
import { initNormalizationService } from "../services/normalization/treeSitterNormalizationService";
import { initVsCodeTelemetryService } from "../services/telemetry/vscodeTelemetry";
import { VSCodeOutputLogger } from "../services/logging/VSCodeOutputLogger";
import { IExtension } from "./extension";
import { ExtendedInlineCompletionItem } from "../@types/inlineCompletionProvider";

// These constants are populated at build time by webpack, and are defined in webpack.config.js.
declare let __PACKAGE_VERSION__: string;
declare let __COMMIT_HASH__: string;

export function defaultExtensionFactory(context: any) {
  return new ExtensionFactory(
    (settings, telemetry, logger) => {
      // Choose which completion service to init based on config
      if (settings.USE_WORKER_THREAD) {
        return initWorkerThreadCompletionService(
          new VSCodeSettings(), // Pass in setting storage backed by vscode's workspace
          telemetry,
          logger
        );
      } else {
        return createLineCompletionServiceWithRetriesAsync(
          settings,
          telemetry,
          logger
        );
      }
    },
    initNormalizationService,
    initVsCodeTelemetryService,
    VSCodeOutputLogger.Instance
  );
}
export class ExtensionFactory {
  private completionState: CompletionState | undefined;
  private telemetry: Telemetry | undefined;

  constructor(
    private initializeCompletionService: (
      settings: Settings,
      telemetry: ITelemetrySender,
      logger: ILoggingService
    ) => Promise<CompletionService>,
    private initializeNormalizationService: (
      logger: ILoggingService
    ) => Promise<INormalizationService>,
    private initTelemetryService: (
      context: vscode.ExtensionContext,
      logger: ILoggingService
    ) => Telemetry,
    private loggingService: ILoggingService
  ) {}

  /**
   * Returns an object containing the activate and deactivate functions.
   * Exporting those two are the minimum requirements of a VSCode extension.
   */
  public get Extension(): IExtension {
    const ext = {
      activate: this.createActivate(),
      deactivate: () => {
        this.telemetry?.reporter.dispose();
      },
    };
    return ext;
  }

  private createActivate() {
    const activate = async (context: vscode.ExtensionContext) => {
      if (this.telemetry === undefined) {
        this.telemetry = this.initTelemetryService(
          context,
          this.loggingService
        );
      }

      // If copilot was enabled before, but isn't anymore, send telemetry
      const copilotPreviouslyEnabled = context.globalState.get(
        constants.COPILOT_ENABLED
      );

      if (copilotPreviouslyEnabled) {
        const copilotEnabled =
          vscode.extensions.getExtension("github.copilot") !== undefined;
        if (!copilotEnabled) {
          this.telemetry.sendTelemetryEvent(
            TelemetryEventNames.COPILOT_DISABLED_AFTER_BEING_ENABLED
          );
        }
      }

      try {
        if (this.completionState === undefined) {
          this.completionState = new CompletionState(
            this.loggingService,
            this.telemetry
          );
        }

        // Create settings instance which is backed by VSCode's settings
        const settings = new Settings(new VSCodeSettings());
        const completionService = await this.initializeCompletionService(
          settings,
          this.telemetry,
          this.loggingService
        );

        context.subscriptions.push(this.telemetry.reporter);
        const activateTelemetryEvent = this.telemetry.startTimedEvent(
          TelemetryEventNames.EXTENSION_ACTIVATED,
          true
        );
        this.telemetry.addContextProperty(
          TelemetryPropertyNames.IS_INTERNAL,
          isInternal()
        );

        // Fix #1429586: If VsCodeVim is present, we need to use a workaround in the post-acceptance command
        // so that inserting a completion which contains a placeholder doesn't eject the user
        // from insert mode. https://github.com/VSCodeVim/Vim/issues/7068
        const vsCodeVimExtension =
          vscode.extensions.getExtension("vscodevim.vim");
        const vsCodeVimIsEnabled = vsCodeVimExtension !== undefined;
        if (vsCodeVimIsEnabled) {
          this.loggingService.write(LogLevel.INFO, "VSCodeVim is installed.");
        }
        context.subscriptions.push(
          postAcceptanceCommand(this.telemetry, vsCodeVimIsEnabled)
        );

        const normalizationService = await this.initializeNormalizationService(
          this.loggingService
        );
        const syntacticFilter = new TreeSitterSyntacticFilterService(
          this.loggingService
        );

        const inlineCompletionProvider = registerInlineCompletionItemProvider(
          context,
          normalizationService,
          syntacticFilter,
          this.loggingService,
          this.telemetry,
          this.completionState,
          completionService,
          settings
        );

        const feedbackSurveyService = new FeedbackSurveyService(context);
        feedbackSurveyService.initializeGlobalStateKeys();
        let timeout: NodeJS.Timeout;

        vscode.window
          .getInlineCompletionItemController(inlineCompletionProvider)
          .onDidShowCompletionItem((e) => {
            // All our completion items are ExtendedInlineCompletionItem, so assert the type.
            const item = e.completionItem as ExtendedInlineCompletionItem;

            this.telemetry?.sendTelemetryEvent(
              TelemetryEventNames.COMPLETION_SHOWN,
              item.properties ?? {},
              item.measures ?? {},
            );

            feedbackSurveyService.incrementNumberOfCompletionsShown();
            if (timeout) {
              clearTimeout(timeout);
            }
            // Show the notification only when the user has been inactive for some time
            timeout = setTimeout(() => {
              feedbackSurveyService.showToastNotification();
            }, USER_IN_REST_STATE_TIME);
          });

        activateTelemetryEvent.end(TelemetryResult.Success);
        this.loggingService.write(
          LogLevel.FATAL,
          `Extension version: ${__PACKAGE_VERSION__} ${__COMMIT_HASH__}`
        );
        const api: ExtensionApi = {
          // API for other extensions to use
          registerDevCommands: () => {
            context.subscriptions.push(
              registerCompletionVisualizationCommand(
                <CompletionState>this.completionState,
                normalizationService
              ),
              registerParseTreeVisualizationCommand(
                <CompletionState>this.completionState,
                normalizationService,
                syntacticFilter,
                this.loggingService,
                settings
              )
            );
          },
          getTelemetryEmitter:
            this.telemetry === undefined
              ? () => {
                  throw new Error("Telemetry Service not intialized");
                }
              : () => this.telemetry!.EventEmitter,
        };

        return api;
      } catch (e) {
        if (!(e instanceof Error)) {
          throw e;
        }
        this.telemetry?.sendFault(
          TelemetryEventNames.EXTENSION_ACTIVATION_FAULT,
          FaultType.Unknown,
          e.message,
          e,
          undefined
        );
        return {
          // Failure scenario. Notify user of failure.
          registerDevCommands: () => {
            vscode.window.showErrorMessage(
              "IntelliCode Completions failed to initialize. DevTools couldn't be initialized.\n" +
                e
            );
          },
          getTelemetryEmitter: () => {
            throw new Error("Telemetry Service not intialized");
          },
        };
      }
    };
    return activate;
  }
}

// Languages current support in WLC
const documentSelector = [
  //"java",
  "javascript",
  "javascriptreact",
  "typescript",
  "typescriptreact",
  "python",
];

function registerInlineCompletionItemProvider(
  context: vscode.ExtensionContext,
  normalizationService: INormalizationService,
  syntacticFilter: TreeSitterSyntacticFilterService,
  loggingService: ILoggingService,
  telemetry: Telemetry,
  completionState: CompletionState,
  lineCompletionService: CompletionService,
  settings: Settings
) {
  const disposable = vscode.commands.registerCommand(
    "extension.inline-completion-settings",
    () => {
      vscode.window.showInformationMessage("Show settings");
    }
  );

  context.subscriptions.push(disposable);

  const intelliCodeInlineCompletionItemProvider: vscode.InlineCompletionItemProvider<vscode.InlineCompletionItem> =
    new IntelliCodeInlineCompletionItemProvider(
      normalizationService,
      syntacticFilter,
      loggingService,
      telemetry,
      completionState,
      lineCompletionService,
      settings
    );

  vscode.languages.registerInlineCompletionItemProvider(
    documentSelector,
    intelliCodeInlineCompletionItemProvider
  );
  return intelliCodeInlineCompletionItemProvider;
}
