import { EventEmitter } from "stream";
import { IdentifiableMessage } from "../workerThread/messages";

/**
 * Helper for sending a request and awaiting a response, where both the request and response share an ID.
 */
export class AwaitableMessageExchanger<TSend extends IdentifiableMessage, TReceive extends IdentifiableMessage> {
    /**
     * Relays any received messages so that an awaiting promise can be resolved.
     */
    private relayEmitter: EventEmitter;

    public constructor(private post: (msg: TSend) => void) {
        this.relayEmitter = new EventEmitter();
    }

    /**
     * Post a request message and resolve a promise when a matching response is returned.
     * Conveniently awaitable.
     * @param outboundMsg Outbound request message.
     * @param completionState The cache that the resulting completion can be stored in.
     * @returns Promise which is resolved when a completion has been cached.
     */
    public exchange(
        outboundMsg: TSend,
    ): Promise<TReceive> {
        try {
            this.post(outboundMsg);
        } catch (e) {
            return Promise.reject(e);
        }

        return new Promise((resolve) => {
            this.relayEmitter.once(
                buildEventName(outboundMsg),
                (response: TReceive) => {
                    resolve(response);
                }
            );
        });
    }

    /**
     * Handles messages that were received, and forwards them on to an awaiter that's
     * waiting for a response with the same message id.
     * This must be called eventually once for each call to exchange(),
     * otherwise that promise will *never* be resolved.
     * @param inboundMsg Message that was received and should be forwarded to an awaiter
     */
    public handleReceivedMessage(inboundMsg: TReceive) {
        this.relayEmitter.emit(
            buildEventName(inboundMsg),
            inboundMsg
        );
    }
}

/**
 * Build internal event names for relayEmitter, used to emit and wait for messages with the right ID.
 * @param msg Message to derive an event name for
 * @returns Event name
 */
export function buildEventName<TMsg extends IdentifiableMessage>(msg: TMsg): string {
    return `message-${msg.requestId}`;
}