import * as vscode from "vscode";
import { CompletionState } from "../../resultCache/completionState";
import { INormalizationService } from "../../services/normalization";
import { getCodeBefore } from "../../utils/vscodeEditor";
import {
  CREATE_VISUALIZATION_COMMAND,
  CREATE_PARSE_TREE_VISUALIZATION_COMMAND,
} from "./ids";
import { showTreeWindow } from "../visualization/window";
import {
  DEFAULT_GRAPH_OPTIONS,
  parsedCodeToGraphData,
} from "../visualization/parsedCode";
import { ILoggingService, LogLevel } from "../../services/logging";
import { TrieNode } from "../../resultCache/hashTrie";
import { ParsedCode } from "../../services/normalization/normalizationService";
import Settings from "../../services/settings/settings";
import { processCompletion } from "../../utils/inlineCompletionProvider/processCompletionItem";
import { createDocumentWithCompletionApplied } from "../../utils/inlineCompletionProvider/validateCompletionItem";
import { FullyProcessedCompletionData } from "../../@types/inlineCompletionProvider";
import {
  TaggedNodes,
  TreeSitterSyntacticFilterService,
} from "../../services/syntacticFilter/treeSitterSyntacticFilter";
import { performance } from "perf_hooks";
import { FromVsCodeRange } from "../../utils/range/conversions";

export function registerCompletionVisualizationCommand(
  completionCache: CompletionState,
  normalizationService: INormalizationService
): vscode.Disposable {
  return vscode.commands.registerCommand(CREATE_VISUALIZATION_COMMAND, () => {
    const context = getCompletionContextFromActiveEditor(normalizationService);
    if (context) {
      const currentTrieNode = completionCache.getCacheTrieNode(context);
      if (currentTrieNode) {
        let graphData = currentTrieNode.getTreeForVisualization();
        showTreeWindow("Completion Visualization", graphData, []);
      }
    }
  });
}

function getCompletionContextFromActiveEditor(
  normalizationService: INormalizationService
): string | undefined {
  const e = vscode.window.activeTextEditor;
  if (e && vscode.window.activeTextEditor) {
    const codeBefore = getCodeBefore(
      vscode.window.activeTextEditor.selection.active,
      vscode.window.activeTextEditor.document
    );
    if (codeBefore) {
      return normalizationService.parseAndNormalize(
        codeBefore,
        e.document.languageId
      );
    }
  }
}

export function registerParseTreeVisualizationCommand(
  completionCache: CompletionState,
  normalizationService: INormalizationService,
  syntacticFilter: TreeSitterSyntacticFilterService,
  loggingService: ILoggingService,
  settings: Settings
): vscode.Disposable {
  return vscode.commands.registerCommand(
    CREATE_PARSE_TREE_VISUALIZATION_COMMAND,
    async () => {
      loggingService.write(LogLevel.INFO, "Creating parse tree");
      const e = vscode.window.activeTextEditor;
      try {
        if (e) {
          let parsedCode: ParsedCode | undefined;

          let aboveGraphText: string[] = [];

          // If there is a completion (provided by the model or by the user in a text box) then insert that into the document before processing, and highlight its nodes.
          const completionContext =
            getCompletionContextFromActiveEditor(normalizationService);
          let completionFromModel: string | undefined;

          // Handle the case where there is a completion in the cache.
          if (completionContext) {
            const start = performance.now();
            // Get the prediction from the cache. Doesn't do all the postprocessing that the actual inlinecompletionprovider does.
            const completion = completionCache.getLineCompletion(
              completionContext,
              settings.getFirstTokenConfidenceThreshold(e.document.languageId),
              Number.NEGATIVE_INFINITY
            );

            if (completion.bestCompletion.length > 0) {
              completionFromModel = completion.bestCompletion;
            }
            const cacheQueryDuration = performance.now() - start;
            loggingService.write(
              LogLevel.INFO,
              `Querying cache: ${cacheQueryDuration}ms`
            );
          }
          // This will be defined if there was a completion- if so, create the resulting document, parse it and highlight the nodes
          let mockCompletion = await vscode.window.showInputBox({
            prompt:
              "Inline completion to be inserted after the cursor (optional)",
            ignoreFocusOut: true,
            value: completionFromModel,
          });

          let postAcceptanceRange: vscode.Range | undefined;
          const codeTillCursor = e.document.getText(
            new vscode.Range(
              e.document.lineAt(e.selection.start.line).range.start,
              e.selection.start
            )
          );

          if (mockCompletion !== undefined && mockCompletion.length > 0) {
            let start = performance.now();
            // build fake completion
            let processedCompletion = processCompletion(
              codeTillCursor,
              undefined,
              undefined,
              undefined,
              e.selection.start,
              mockCompletion
            );
            loggingService.write(
              LogLevel.INFO,
              `Processing completion: ${performance.now() - start}ms`
            );

            start = performance.now();
            const resultingDocument = createDocumentWithCompletionApplied(
              e.document,
              e.selection.start,
              processedCompletion.additionalData.fullLineWithCompletion
            );
            loggingService.write(
              LogLevel.INFO,
              `Creating document with completion applied: ${
                performance.now() - start
              }ms`
            );
            start = performance.now();
            parsedCode = normalizationService.parse(
              resultingDocument,
              e.document.languageId
            );
            loggingService.write(
              LogLevel.INFO,
              `Parsed document with completion applied: ${
                performance.now() - start
              }ms`
            );
            aboveGraphText.push(
              `Graph for ${e.document.fileName} with prediction '${mockCompletion}' resulting in full line: <pre>${processedCompletion.additionalData.fullLineWithCompletion}</pre>`
            );
            postAcceptanceRange =
              processedCompletion.additionalData.postAcceptanceRange;
          }

          let startUnmodifiedParse = performance.now();
          let unmodifiedParsedCode = normalizationService.parse(
            e.document.getText(),
            e.document.languageId
          );
          loggingService.write(
            LogLevel.INFO,
            `Parsed original document: ${
              performance.now() - startUnmodifiedParse
            }ms`
          );
          if (!parsedCode) {
            // If there wasn't a prediction, just parse the whole document.
            aboveGraphText.push(`Graph for ${e.document.fileName}`);
          }

          // Tag nodes
          let nodeLists: TaggedNodes;
          let parsedCodeToDisplay: ParsedCode;
          if (!parsedCode) {
            let start = performance.now();
            nodeLists = syntacticFilter.tagSyntaxNodes(
              unmodifiedParsedCode,
              undefined,
              FromVsCodeRange(postAcceptanceRange)
            );
            loggingService.write(
              LogLevel.INFO,
              `Tagged nodes of unmodified tree: ${performance.now() - start}ms`
            );
            parsedCodeToDisplay = unmodifiedParsedCode;
          } else {
            let start = performance.now();
            nodeLists = syntacticFilter.tagSyntaxNodes(
              parsedCode,
              unmodifiedParsedCode,
              FromVsCodeRange(postAcceptanceRange)
            );
            loggingService.write(
              LogLevel.INFO,
              `Tagged nodes of modified tree and changes from unmodified tree: ${
                performance.now() - start
              }ms`
            );
            parsedCodeToDisplay = parsedCode;
          }

          let startBuildGraphData = performance.now();
          const graphData = parsedCodeToGraphData(
            parsedCodeToDisplay,
            nodeLists,
            DEFAULT_GRAPH_OPTIONS
          );
          loggingService.write(
            LogLevel.INFO,
            `Built graph data: ${performance.now() - startBuildGraphData}ms`
          );

          showTreeWindow("Parse Tree Visualization", graphData, aboveGraphText);
        } else {
          loggingService.write(LogLevel.ERROR, "No active editor");
        }
      } catch (e) {
        if (e instanceof Error) {
          loggingService.write(
            LogLevel.ERROR,
            `Error creating parse tree visualization: \n${e.message}\n${e.stack}`
          );
        }
      }
    }
  );
}
