export function removeOverlappingWhiteSpace(
  codeTillCursor: string,
  completion: string
) {
  return codeTillCursor.endsWith(" ") && completion.startsWith(" ")
    ? completion.slice(1)
    : completion;
}

export function isPreexistingCode(noMaskCompletion: string, codeAfter: string) {
  return noMaskCompletion === codeAfter;
}

export function isSuffixedByCodeAfter(completion: string, codeAfter: string) {
  return completion.endsWith(codeAfter);
}

export function codeAfterWithinCompletion(
  completion: string,
  codeAfter: string
) {
  return completion.indexOf(codeAfter) !== -1;
}

/**
 * Due to a bug during training, #<COMMENT> tokens are now in the model.
 *
 * This has also been implemented as a post-processing rule as #<COMMENT> is actually 4 tokens,
 * making it extremely difficult to ignore during caching.
 *
 * Remove this function when the model has been fixed.
 *
 * @param completion completion to remove #<COMMENT> tokens from
 * @returns string without #<COMMENT> tokens
 */
export function removeIndentDedentToken(completion: string) {
  return completion.replace(/#<COMMENT>|<INDENT>|<DEDENT>/g, "");
}

const bracesMap = new Map<string, string>([
  ["(", ")"],
  ["{", "}"],
  ["[", "]"],
]);
/**
 * Heuristic checking of (, {, [ that need to be matched.
 * Only deals with simple cases, and ignores unbalanced braces.
 * @param unmatchedString String with potential unmatched braces
 * @returns String of braces that need to be appended at the end
 */
export function matchBraces(unmatchedString: string) {
  const bracesStack: string[] = [];
  for (const c of unmatchedString) {
    if (bracesMap.get(bracesStack[bracesStack.length - 1]) === c) {
      bracesStack.pop();
    } else if (bracesMap.has(c)) {
      bracesStack.push(c);
    }
  }

  let requiredBraces = "";
  while (bracesStack.length !== 0) {
    const unmatchedBrace = bracesStack.pop();
    if (!unmatchedBrace) {
      break;
    }
    requiredBraces += bracesMap.get(unmatchedBrace);
  }
  return requiredBraces;
}
