import { window } from "vscode";
import Settings from "../settings/settings";
import { VSCodeSettings } from "../settings/vscodeSettings";
import { ILoggingService, LogLevel } from "./loggingService";

export class VSCodeOutputLogger implements ILoggingService {
  private static singleton: VSCodeOutputLogger;
  private channel = window.createOutputChannel("IntelliCode Completions");

  private settings = new Settings(new VSCodeSettings());

  public static get Instance(): VSCodeOutputLogger {
    if (!VSCodeOutputLogger.singleton) {
      VSCodeOutputLogger.singleton = new VSCodeOutputLogger();
    }
    return VSCodeOutputLogger.singleton;
  }

  public write(logLevel: LogLevel, str: string): void {
    if (logLevel >= LogLevel[this.settings.LOG_LEVEL]) {
      this.channel.appendLine(str);
    }
  }
}

const { Instance } = VSCodeOutputLogger;
export { Instance };
