import * as vscode from "vscode";
import ISurveyService from "./surveyService";
import * as constants from "./index";

export default class FeedbackSurveyService implements ISurveyService {
  context: vscode.ExtensionContext;

  constructor(extensionContext: vscode.ExtensionContext) {
    this.context = extensionContext;
  }

  public initializeGlobalStateKeys() {
    this.context.globalState.setKeysForSync([
      constants.DO_NOT_SHOW_AGAIN_SELECTED,
      constants.NUMBER_OF_COMPLETIONS_SHOWN,
      constants.NOTIFICATION_ALREADY_SHOWN,
      constants.SHOW_NOTIFICATION_AT_TIME,
      constants.LAST_TIME_USER_WAS_ACTIVE,
    ]);
  }

  public async showToastNotification() {
    if (!this.shouldShowNotification()) {
      return;
    }

    const options = [
      constants.NOTIFICATION_RESPONSE_YES,
      constants.NOTIFICATION_RESPONSE_MAYBE_LATER,
      constants.NOTIFICATION_RESPONSE_DO_NOT_SHOW_AGAIN,
    ];
    this.context.globalState.update(constants.NOTIFICATION_ALREADY_SHOWN, true);
    const selection = await vscode.window.showInformationMessage(
      constants.FEEDBACK_MESSAGE,
      ...options
    );

    if (!selection) {
      return;
    }

    if (selection === constants.NOTIFICATION_RESPONSE_YES) {
      vscode.env.openExternal(vscode.Uri.parse(constants.FEEDBACK_SURVEY_URI));
    } else if (selection === constants.NOTIFICATION_RESPONSE_MAYBE_LATER) {
      // Show notification after some time
      this.context.globalState.update(
        constants.SHOW_NOTIFICATION_AT_TIME,
        new Date().getTime() + constants.MAYBE_LATER_WAIT_TIME
      );
      this.context.globalState.update(
        constants.NOTIFICATION_ALREADY_SHOWN,
        false
      );
    } else if (
      selection === constants.NOTIFICATION_RESPONSE_DO_NOT_SHOW_AGAIN
    ) {
      this.context.globalState.update(
        constants.NOTIFICATION_RESPONSE_DO_NOT_SHOW_AGAIN,
        true
      );
    }
  }

  private shouldShowNotification(): boolean {
    if (
      this.context.globalState.get(
        constants.NOTIFICATION_RESPONSE_DO_NOT_SHOW_AGAIN
      )
    ) {
      return false;
    }

    if (this.context.globalState.get(constants.NOTIFICATION_ALREADY_SHOWN)) {
      return false;
    }

    const oldNumberOfCompletionsShown: number =
      this.context.globalState.get(constants.NUMBER_OF_COMPLETIONS_SHOWN) ?? 0;
    if (
      oldNumberOfCompletionsShown <
      constants.CAN_SHOW_SURVEY_AFTER_NUMBER_OF_COMPLETIONS
    ) {
      return false;
    }

    const timeNow: number = new Date().getTime();
    const showNotificationWhenTime: number =
      this.context.globalState.get(constants.SHOW_NOTIFICATION_AT_TIME) ?? 0;
    if (timeNow < showNotificationWhenTime) {
      return false;
    }

    return true;
  }

  public incrementNumberOfCompletionsShown() {
    const oldNumberOfCompletionsShown: number =
      this.context.globalState.get(constants.NUMBER_OF_COMPLETIONS_SHOWN) ?? 0;
    this.context.globalState.update(
      constants.NUMBER_OF_COMPLETIONS_SHOWN,
      oldNumberOfCompletionsShown + 1
    );
  }
}
