import * as LRUCache from "lru-cache";
import * as uuid from "uuid";
import { createHash } from "crypto";
import { GraphData } from "../vscodeExtension/visualization/graphData";
import { Edge, IdType, Node } from "vis-network";

export default class HashTrie {
  public prefixMapping = new LRUCache<string, TrieNode>(1000);

  /**
   * Stores suffixes in a Trie cache relating each to the correct prefix.
   * @param prefix
   * @param suffix
   * @param score Log confidence scores
   */
  public insert(prefix: string, suffix: string[], score: number[]) {
    let val = this.prefixMapping.get(prefix);
    if (!val) {
      val = new TrieNode();
      this.prefixMapping.set(prefix, val);
    }
    val.insert(suffix, score);
  }

  public getAllSuffixes(prefix: string, minLength: number) {
    const val = this.prefixMapping.get(prefix);
    if (val) {
      const suffixes: string[] = [];
      val.getAllSuffixes(suffixes);
      return suffixes.filter((s) => s.length > minLength);
    } else {
      return [];
    }
  }

  /**
   *
   * @param prefix Context to look for
   * @param firstTokenThreshold Don't search unless first token's score is above this value
   * @param logCutoff Log confidence threshold to stop searching the tree
   * @returns [bestRecommendation, firstTokenScore, cumulativeFinalTokenScore, tokenCount]
   */
  public getBestSuffix(
    prefix: string,
    firstTokenThreshold: number,
    logCutoff: number
  ): [string, number, number, number] {
    const val: TrieNode | undefined = this.prefixMapping.get(prefix);

    if (!val) {
      return ["", 0, 0, 0];
    }
    const firstTokenScore = val.getBestChild().score;
    if (firstTokenScore > firstTokenThreshold) {
      const [suggestion, lastTokenCumulativeScore, tokenCount] =
        val.getBestSuffix(logCutoff);
      return [
        suggestion,
        firstTokenScore,
        lastTokenCumulativeScore,
        tokenCount,
      ];
    } else {
      return ["", firstTokenScore, 0, 0];
    }
  }
}

export class TrieNode {
  public char = "\0";
  public score = 0;

  /**
   * Children of this node, accessible by key.
   */
  public children: { [k: string]: TrieNode } = {};

  constructor(char: string = "\0", score = 0) {
    this.char = char;
    this.score = score;
  }

  public getBestChild(): TrieNode {
    const children = Object.values(this.children);
    // this is essentially a max function keying on the score
    return children.reduce((best, curr) =>
      best.score > curr.score ? best : curr
    );
  }

  /**
   * Insert a child trie node containing a subtoken and its score
   * @param s The subtoken to insert underneath this trie node
   * @param score The new node's score
   * @param i The index of the beginning of the subtoken within the overall string
   */
  public insert(s: string[], score: number[], i = 0) {
    if (i < s.length) {
      let currentToken = s[i];
      let currentScore = score[i];

      while (i < s.length && !currentToken) {
        currentToken = s[++i];
      }
      if (
        currentToken &&
        currentToken !== "<endofline>" &&
        currentToken !== "<endoftext>" &&
        currentToken !== "<unk>"
      ) {
        let childName = createHash("sha1")
          .update(currentToken)
          .digest("base64");
        if (!this.children.hasOwnProperty(childName)) {
          this.children[childName] = new TrieNode(currentToken, currentScore);
        } else if (this.children[childName].score !== currentScore) {
          // Update probabilities assigned from older runs of the model
          this.children[childName].score = currentScore;
        }
        this.children[childName].insert(s, score, i + 1);
      }
    }
  }

  public getAllSuffixes(suffixes: string[], s: string = "") {
    if (this.char !== "\0") {
      s += this.char;
    }
    if (this.hasNoChildren()) {
      suffixes.push(s);
    } else {
      for (const [, v] of Object.entries(this.children)) {
        v.getAllSuffixes(suffixes, s);
      }
    }
  }

  /**
   *
   * @param logCutoff Log confidence threshold at which to stop searching tree
   * @returns [bestRecommendation, cumulativeFinalTokenScore, tokenCount]
   */
  public getBestSuffix(logCutoff: number): [string, number, number] {
    return this.getBestSuffixHelper(logCutoff);
  }

  /**
   *
   * @param context context
   * @param logCutoff Log confidence threshold to stop searching tree
   * @returns best recommendation within cache
   */
  private getBestSuffixHelper(
    logCutoff: number,
    context: string = "",
    cumulativeScore: number = 0,
    tokenCount: number = 0
  ): [string, number, number] {
    if (this.char !== "\0") {
      context += this.char;
      cumulativeScore += this.score;
      tokenCount += 1;
    }
    if (this.hasNoChildren()) {
      return [context, cumulativeScore, tokenCount];
    }

    const bestChild = this.getBestChild();
    if (bestChild.score >= logCutoff) {
      return bestChild.getBestSuffixHelper(
        logCutoff,
        context,
        cumulativeScore,
        tokenCount
      );
    } else {
      // complete the string
      let n: TrieNode = this;

      while (n.char.match(/^[a-zA-Z0-9_]/)) {
        n = this.getBestChild();

        context += n.char;
        cumulativeScore += n.score;
        tokenCount += 1;
        if (Object.entries(n.children).length === 0) {
          break;
        }
      }

      return [context, cumulativeScore, tokenCount];
    }
  }

  public getTreeForVisualization() : GraphData {
    const nodes: Node[] = [];
    const edges: Edge[] = [];
    this.populateVisualizationDataset(nodes, edges, undefined);

    return {
      nodes: nodes,
      edges: edges,
      options: {
        autoResize: true,
        height: '1200px',
        clickToUse: false,
        layout: {
          hierarchical: {
            direction: 'LR',
            sortMethod: 'directed',
            //levelSeparation: 400,
            nodeSpacing: 50,
          },
        },
        interaction: {
          multiselect: true
        },
        nodes: {
          color: {
            highlight: {
              border: '#FF0000'
            }
          },
          size: 15,
          font: {
            size: 20,
            color: '#000000',
            face: 'sans-serif',
            background: "#FFFFFF00",
            align: "center",
            strokeColor: "#00000000",
            strokeWidth: 0,
          },
          shape: 'box'
        },
        physics: {
          barnesHut: {
            springConstant: 0,
            avoidOverlap: 0.2
          }
        }
      }
    };
  }

  private populateVisualizationDataset(
    nodes: Node[],
    edges: Edge[],
    parentId: IdType | undefined,
    append = false
  ) {
    let nodeId;
    if (append) {
      nodes[nodes.length - 1].label += this.char;
      nodeId = nodes[nodes.length - 1].id;
    } else {
      nodeId = uuid.v4();
      const thisNode = {
        id: nodeId,
        // label: this.char === "\0" ? "<START>" : this.char
        label: `${
          this.char === "\0" ? "<START>" : this.char
        } ${this.score.toExponential(2)}`,
      };
      nodes.push(thisNode);
      if (parentId !== null) {
        edges.push({ from: parentId, to: nodeId });
      }
    }

    const childrenObjs = Object.entries(this.children);
    for (const [, v] of childrenObjs) {
      v.populateVisualizationDataset(
        nodes,
        edges,
        nodeId,
        false // this.char !== "\0" && childrenObjs.length === 1
      );
    }
  }

  private hasNoChildren() {
    return Object.keys(this.children).length === 0;
  }

  private leafCount() {
    if (this.hasNoChildren()) {
      return 1;
    } else {
      let sum = 0;
      for (const [, v] of Object.entries(this.children)) {
        sum += v.leafCount();
      }
      return sum;
    }
  }

  private childCount() {
    if (this.hasNoChildren()) {
      return 1;
    } else {
      let sum = 1;
      for (const [, v] of Object.entries(this.children)) {
        sum += v.childCount();
      }
      return sum;
    }
  }

  private getHeight() {
    let depth = 0;
    for (const child in this.children) {
      const childDepth = this.children[child].getHeight();
      if (depth < childDepth) {
        depth = childDepth;
      }
    }
    return depth + 1;
  }
}
