import * as vscode from "vscode";
import {
  ModifiedCompletionResult,
} from "../../@types/inlineCompletionProvider";

/**
 * Returns true if the user moved to a different document
 */
export function hasDocumentChanged(
  prevDocument: vscode.TextDocument,
  currDocument: vscode.TextDocument
) {
  return !(prevDocument === currDocument);
}

/**
 * Returns true if the user moved to a different line
 */
export function hasLineNumberChanged(prevLine: number, currLine: number) {
  return !(prevLine === currLine);
}

/**
 * Returns if user is walking the prediction
 * @param hasDocumentChanged Whether the document has changed since the last prediction
 * @param hasLineNumberChanged Whether the line number has changed since the last prediction
 * @param currSelectedCompletion The current item selected in the IntelliSense menu
 * @param prevSelectedCompletion The previous item selected in the IntelliSense menu
 * @param prevFullLineCompletion The full line with the last completion provided
 * @param currLine The text on the current line
 * @param lineTillCursor The text on the line till the cursor
 * @returns True if user is walking the prediction, false otherwise
 */
export function walkingThePredictionStatus(
  hasDocumentChanged: boolean,
  hasLineNumberChanged: boolean,
  currSelectedCompletion: string | undefined,
  prevSelectedCompletion: string | undefined,
  prevFullLineCompletion: string,
  currLine: string,
  lineTillCursor: string
): boolean {
  if (hasDocumentChanged || hasLineNumberChanged) {
    return false;
  }

  /**
   * Sometimes VS Code returns the selected completion menu with a "." before the text
   * Removing the "." if it is there so that the equality check works
   */
  if (currSelectedCompletion?.startsWith(".")) {
    currSelectedCompletion = currSelectedCompletion.substring(1);
  }
  if (prevSelectedCompletion?.startsWith(".")) {
    prevSelectedCompletion = prevSelectedCompletion.substring(1);
  }
  /**
   * If the user changes their selected in the completion menu
   */
  if (currSelectedCompletion != prevSelectedCompletion) {
    return false;
  }

  /**
   * Removing extra white spaces.
   * If the prediction is `const abc` and the user has typed `const  abc` (notice 2 spaces between const and abc),
   * we'll still be able to say the user is walking the prediction
   * */
  currLine = currLine.replace(/\s+/g, " ");
  lineTillCursor = lineTillCursor.replace(/\s+/g, " ");
  prevFullLineCompletion = prevFullLineCompletion.replace(/\s+/g, " ");

  if (
    currLine &&
    prevFullLineCompletion.startsWith(lineTillCursor) &&
    currLine != prevFullLineCompletion
  )
    return true;
  else {
    return false;
  }
}

/**
 * When the user is walking the prediction i.e. typing the same characters as those displayed in the prediction, we need to return the remaining prediction that the user hasn't typed/accepted yet
 * @param currLine The text on the current line
 * @param positionLine Current line position of cursor
 * @param prevFullLineCompletion Full line with the previously provided completion
 * @returns Remaining prediction i.e. token the user is typing over + remaining tokens
 */
export function modifyPredictionForWalking(
  currLine: string,
  positionLine: number,
  prevFullLineCompletion: string
): ModifiedCompletionResult | undefined {
  if (!prevFullLineCompletion) {
    return undefined;
  }
  /**
   * Number of spaces in the text on the current line.
   * Multiple consecutive spaces will be counted as 1 space
   */
  const numSpacesInLineTillCaret: number = (currLine.match(/ /g) || []).length;
  for (let i = 0; i < currLine.length; i++) {
    if (currLine[i] == " " && currLine[i] != prevFullLineCompletion[i]) {
      prevFullLineCompletion = [
        prevFullLineCompletion.slice(0, i),
        " ",
        prevFullLineCompletion.slice(i),
      ].join("");
    }
  }
  const tokens: string[] = prevFullLineCompletion.split(" ");
  /**
   * The starting position of the current token in the line
   * Length of all tokens in the string + number of spaces in the string
   */
  const currTokenStartColumn: number =
    tokens.slice(0, numSpacesInLineTillCaret).join("").length +
    numSpacesInLineTillCaret;

  /**
   * If the user has typed some of the prediction, we need to show the remaining prediction
   * The prediction is truncated to include the current token and beyond
   * For example, if the full prediction is `const abc = method()`
   * and the user has typed `const a`, we will return `abc = method()`
   */
  const truncatedCompletionString = tokens
    .slice(numSpacesInLineTillCaret)
    .join(" ");

  /**
   * The entire range of the token in the user's document needs to be replaced
   * For example, if the prediction is `const abc = method()` and the user has typed `const a`, the truncated prediction will be `abc = method()`
   * Instead of inserting `bc = method()`, we will actually replace `a` with `abc = method` and the range of `a` needs to be provided
   */
  const modifiedResult: ModifiedCompletionResult = {
    newText: truncatedCompletionString,
    newRange: new vscode.Range(
      new vscode.Position(positionLine, currTokenStartColumn),
      new vscode.Position(
        positionLine,
        currTokenStartColumn + truncatedCompletionString.length
      )
    ),
  };
  return modifiedResult;
}

export function modifyPreviousCompletionProvided(
  prevFullLineCompletion: string,
  prevSnippetString: string,
  prevSnippetRange: vscode.Range,
  prevCompletionItemText: string, 
  prevCompletionItemRange: vscode.Range | undefined,
  codeOnLine: string,
  positionLine: number
) {
  const modifiedCompletion = modifyPredictionForWalking(
    codeOnLine,
    positionLine,
    prevFullLineCompletion
  );

  const modifiedSnippet = modifyPredictionForWalking(
    codeOnLine,
    positionLine,
    prevSnippetString
  );

  return {
    newCompletionText: modifiedCompletion
      ? modifiedCompletion.newText
      : prevCompletionItemText,
    newCompletionRange: modifiedCompletion
      ? modifiedCompletion.newRange
      : prevCompletionItemRange,
    newSnippetText: modifiedSnippet
      ? modifiedSnippet.newText
      : prevSnippetString,
    newSnippetRange: modifiedSnippet
      ? modifiedSnippet.newRange
      : prevSnippetRange,
  };
}
