export function isPythonFunctionSignature(code: string) {
  const funcSigRegex = /def (\w+)\s*\((.*?)\):/;
  return funcSigRegex.test(code);
}

export enum LanguageId {
  JAVA = "java",
  JAVASCRIPT = "javascript",
  JAVASCRIPT_REACT = "javascriptreact",
  PYTHON = "python",
  TYPESCRIPT = "typescript",
  TYPESCRIPT_REACT = "typescriptreact",
}