import * as vscode from "vscode";
import { POST_ACCEPTANCE_COMMAND } from "./ids";
import {
  TelemetryEventNames,
  TelemetryMeasureNames,
  TelemetryPropertyNames,
} from "../../@types/telemetry";
import { ITelemetrySender } from "../../services/telemetry/sender";
import { postAcceptanceCommandArguments } from "../../@types/inlineCompletionProvider";

/**
 * Registering the command and the command handler to be run when the user accepts a prediction
 */
export default (
  telemetry: ITelemetrySender,
  useVsCodeVimSnippetWorkaround: boolean
) =>
  /**
   * Deals with actions that need to take place after the completion has been accepted by the user.
   * Currently carries out 2 actions:
   *   1. Send telemetry
   *   2. Append the given string to behind the cursor
   * @param telemetryEventName Telemetry event to send
   * @param telemetryProperties Telemetry properties to send
   * @param telemetryMeasures Telemetry measures to send
   * @param editor The editor to add {@link postAcceptanceInsertion} to
   * @param postAcceptanceInsertion String to append to behind where the cursor is after the completion has been accepted
   * @param overwritePosition If this value is populated, postAcceptanceInsertion will overwrite the rest of the line starting from this position.
   */
  vscode.commands.registerCommand(
    POST_ACCEPTANCE_COMMAND,
    async (postAcceptanceCommandArguments: postAcceptanceCommandArguments) => {
      const {
        languageId: languageId,
        completionLength: completionLength,
        firstTokenScore: firstTokenScore,
        cumulativeLastTokenScore: cumulativeLastTokenScore,
        tokenCount: tokenCount,
        lengthPenalizedLastTokenScore: lengthPenalizedLastTokenScore,
        snippetPlaceholderCount: snippetPlaceholderCount,
        editor: editor,
        postInsertionString: postInsertionString,
        overwriteRange: overwriteRange,
      } = postAcceptanceCommandArguments;

      if (editor && postInsertionString) {
        // Fix #1429586: Work around an issue in VSCodeVim where inserting a snippet over a non-empty location
        // will exit insert mode. This is extremely disruptive to vim users. https://github.com/VSCodeVim/Vim/issues/7068
        // Instead, we first delete the overwrite range and then insert the snippet at the beginning position of the overwrite range.
        // We avoid this workaround if the user doesn't have VSCodeVim. I'm not sure if it has any adverse effects,
        // but seems reasonable to me that we should try to use insertSnippet as it is designed except in the one case
        // where we need to work around the issue.
        if (useVsCodeVimSnippetWorkaround && overwriteRange) {
          await editor.edit((edit) => {
            edit.delete(overwriteRange);
          });

          await editor.insertSnippet(
            new vscode.SnippetString(postInsertionString),
            overwriteRange.start,
            {
              undoStopBefore: false, // Ctrl+Z takes users back to before InlineCompletion
              undoStopAfter: true,
            }
          );
        } else {
          await editor.insertSnippet(
            new vscode.SnippetString(postInsertionString),
            overwriteRange,
            {
              undoStopBefore: false, // Ctrl+Z takes users back to before InlineCompletion
              undoStopAfter: true,
            }
          );
        }
      }

      const telemetryEventName = TelemetryEventNames.COMPLETION_ACCEPTED;
      const telemetryProperties = {
        [TelemetryPropertyNames.LANGUAGE_ID]: languageId,
      };
      const telemetryMeasures = {
        [TelemetryMeasureNames.COMPLETION_LENGTH]: completionLength,
        [TelemetryMeasureNames.FIRST_TOKEN_SCORE]: firstTokenScore,
        [TelemetryMeasureNames.CUMULATIVE_LAST_TOKEN_SCORE]:
          cumulativeLastTokenScore,
        [TelemetryMeasureNames.TOKEN_COUNT]: tokenCount,
        [TelemetryMeasureNames.LENGTH_PENALIZED_LAST_TOKEN_SCORE]:
          lengthPenalizedLastTokenScore,
        [TelemetryMeasureNames.SNIPPET_PLACEHOLDER_COUNT]:
          snippetPlaceholderCount,
      };

      telemetry.sendTelemetryEvent(
        telemetryEventName as string,
        telemetryProperties,
        telemetryMeasures
      );
    }
  );
