import { LanguageId } from "../../utils/language";
import { ILoggingService, LogLevel } from "../logging";
import { join } from "path";
import { libFolder } from "../../resolvedPath";
import * as WebParser from "web-tree-sitter";

enum RuntimeState {
  "NotInit",
  "Failed",
  "Active",
}

/**
 * Some languages (JSX, TSX) have a language id defined by VSCode,
 * but for have different names on Tree Sitter.
 * This function takes care of those differences.
 * @param langId The language to get correlating wasm name for
 */
function languageIdToWasmName(langId: LanguageId) {
  switch (langId) {
    case LanguageId.JAVASCRIPT_REACT:
      return LanguageId.JAVASCRIPT;
    case LanguageId.TYPESCRIPT_REACT:
      return "tsx";
    default:
      return langId;
  }
}

export class TreeSitterManager {
  private runtimeState: RuntimeState = RuntimeState.NotInit; // State for tree sitter runtime initialization
  private runtime: any; // Tree Sitter runtime will be saved here after initialization

  public availableLanguages: LanguageId[] = [];
  public trees: { [key in LanguageId]?: WebParser.Language } = {};

  public static async buildTreeSitterManager(logger: ILoggingService) {
    let self = new TreeSitterManager(logger);
    await self.initializeRuntime();

    if (self.runtimeState !== RuntimeState.Active) {
      throw new Error("Tree Sitter could not be initialized.");
    }
    return self;
  }

  private constructor(private Logger?: ILoggingService) {}

  public createParser(): WebParser {
    if (this.runtimeState !== RuntimeState.Active) {
      this.Logger?.write(
        LogLevel.ERROR,
        "[treeSitterManager:Parser()] Parser accessed when not available."
      );
      throw new Error("Parser accessed but Tree Sitter is not available.");
    }
    return new this.runtime() as WebParser;
  }

  /*
   * Returns the tree-sitter for the specified language.
   * This method does not check if the tree-sitter for the language has been loaded.
   */
  public get(lang: LanguageId): WebParser.Language {
    const langTree = this.trees[lang];
    if (langTree === undefined) {
      this.Logger?.write(
        LogLevel.ERROR,
        `[treeSitterManager:get()] ${lang} tree accessed when not available.`
      );

      throw new Error(`${lang} tree accessed when not available.`);
    }
    return langTree;
  }

  /**
   * Checks if the tree-sitter for a certain language has been downloaded and initialized.
   * @param lang Language to check availability
   * @returns boolean representing availability
   */
  public isAvailableFor(lang: LanguageId): boolean {
    return this.availableLanguages.indexOf(lang) !== -1;
  }

  /*
   * Loads the tree-sitter runtime.
   *
   * Currently called once in the constructor and nowhere else.
   * DO NOT call in other places.
   *   1. There is no reason to call this again
   *   2. The Promise returned from this function is what keeps the concurrency of all other tree-sitter fetching
   */
  private async initializeRuntime() {
    if (this.runtimeState !== RuntimeState.NotInit) {
      this.Logger?.write(
        LogLevel.ERROR,
        "[treeSitterManager:initializeRuntime()] Tree Sitter re-initialized. This shouldn't happen."
      );
      throw new Error("Tree Sitter was initialized more than once.");
    }
    try {
      await WebParser.init();
      this.runtime = WebParser;
      this.runtimeState = RuntimeState.Active;
    } catch (error) {
      this.runtimeState = RuntimeState.Failed;
      this.Logger?.write(
        LogLevel.ERROR,
        `[treeSitterManager:initializeRuntime()] Tree Sitter failed to initialize: ${error}`
      );
    }
  }

  private checkSupport(lang: string) {
    return (<any>Object).values(LanguageId).includes(lang);
  }

  /*
   * Does nothing if the tree-sitter runtime failed to be loaded.
   * If the runtime hasn't failed yet, it will load the tree-sitter for the specific language.
   */
  public async initialize(lang: LanguageId) {
    if (this.isAvailableFor(lang)) {
      // Already initialized. No need to do extra work.
      return;
    }
    if (!this.checkSupport(lang)) {
      this.Logger?.write(
        LogLevel.ERROR,
        `[TreeSitterManager.initialize()] ${lang} isn't supported.`
      );
      throw new Error(`${lang} isn't supported.`);
    }

    const langWasmPath = join(
      libFolder,
      `tree-sitter-${languageIdToWasmName(lang)}.wasm`
    );
    try {
      const languageTree = await WebParser.Language.load(langWasmPath);
      if (
        lang === LanguageId.JAVASCRIPT ||
        lang === LanguageId.JAVASCRIPT_REACT
      ) {
        this.trees[LanguageId.JAVASCRIPT] = this.trees[
          LanguageId.JAVASCRIPT_REACT
        ] = languageTree;
      } else {
        this.trees[lang] = languageTree;
      }
      this.availableLanguages.push(lang);
    } catch (e) {
      this.Logger?.write(
        LogLevel.ERROR,
        `[TreeSitterManager.initialize()] ${e}`
      );
      throw e;
    }
  }
}
