import { performance } from "perf_hooks";
import { OnnxModelWrapper } from "../model/onnxModelWrapper";
import { ILoggingService, LogLevel } from "../logging";
import { RequestResult } from "./abstractCompletionService";
import Settings from "../settings/settings";
import { join } from "path";
import { modelFolder } from "../../resolvedPath";
import { AbstractCompletionService } from "./abstractCompletionService";
import { ITelemetrySender } from "../telemetry/sender";
import { ICompletionResultCallback } from "../../resultCache/completionResultCallback";
import {
  TelemetryEventNames,
  TelemetryMeasureNames,
  TelemetryPropertyNames,
} from "../../@types/telemetry";

export const modelPath = join(modelFolder, "composeModelQuantized.onnx");
export const configPath = join(modelFolder, "metadata.json");
export const tokenizerPath = join(modelFolder, "bpe.bin");
export const vocabPath = join(modelFolder, "vocab.txt");

export const blingfireRaceConditionErrMsg =
  "TypeError: Cannot read property 'W' of undefined";

const NUM_RETRIES = 10;

export async function createLineCompletionServiceWithRetriesAsync(
  settings: Settings,
  telemetry: ITelemetrySender,
  logger: ILoggingService
) {
  /**
   * Currently Blingfire has a race condition due to emscripten.
   * lib/blingfire_wrapper.js:LoadLocalModel():57 is what will cause the error to happen.
   * This retry logic will counteract the race condition.
   *
   * FULL EXPLANATION:
   * In lib/blingfire.js:1325, Module["_malloc"] = Module["asm"]["W"] is executed.
   * However, it's possible that this is called too early and Module["asm"] is NOT initialized.
   * This is supposed to be initialized in blingfire.js:createWasm():439,
   * but this function execution is asynchronous as it deals with compiling the binary .wasm file.
   *
   * The Module exported from blingfire has life cycle events one can use,
   * namely onRuntimeInitialized, postRun, addOnPostRun, and the kind.
   * However these life cycle events are designed to be hooked on to during emscripten compile time,
   * thorugh build flags such as --pre-js <filename> or --post-js <filename>.
   * Hooking on to those life cycle events during runtime puts us at another race condition risk of
   * having attached to the event too late, leading to our function never being activated.
   *
   * UNEXPLORED PATHS:
   * There exists at least two ways to make the instatiation process synchronous.
   *   1. Build without Wasm in pure JS
   *   2. Build with synchronous activation flag
   * This path was left unexplored as not only did these require rebuilding blingfire,
   * there was also no guarantee that synchronous instatiation would solve our issue.
   * As long as blingfire_wrapper was how we interfaced with blingfire,
   * the issue remains that we have no way to know what the progress is on instantiating the wasm.
   */
  for (let i = 0; i < NUM_RETRIES; i++) {
    try {
      let onnxModel = await OnnxModelWrapper.load(
        modelPath,
        configPath,
        tokenizerPath,
        vocabPath,
        settings,
        logger
      );
      return new LocalLineCompletionService(
        onnxModel,
        telemetry,
        settings,
        logger
      );
    } catch (e) {
      if (!(e instanceof Error)) {
        throw e;
      }
      if (e.message.split("\n")[1].startsWith(blingfireRaceConditionErrMsg)) {
        await new Promise((resolve) => setTimeout(resolve, 100 * i));
        continue;
      } else {
        throw e;
      }
    }
  }
  throw new Error(
    `Could not create line completion service after ${NUM_RETRIES} attempts`
  );
}

/**
 * A service which uses OnnxModelWrapper in the current process to infer predictions from a model.
 */
export class LocalLineCompletionService extends AbstractCompletionService {
  public constructor(
    private onnxExec: OnnxModelWrapper,
    private telemetry: ITelemetrySender,
    private settings: Settings,
    Logger: ILoggingService
  ) {
    super(Logger);
  }

  public async request(
    requestId: number,
    normalizedContext: string,
    lang: string,
    completionState: ICompletionResultCallback,
    cancellationToken: { isCancellationRequested: boolean }
  ): Promise<RequestResult> {
    const logPrefix = `[LocalLineCompletionService.request() ${requestId}] `;

    this.Logger.write(LogLevel.DEBUG, `${logPrefix} Entered`);
    if (cancellationToken.isCancellationRequested) {
      this.Logger.write(LogLevel.DEBUG, `${logPrefix} Cancelled at beginning`);
      return RequestResult.Cancelled;
    }
    if (!normalizedContext) {
      throw new Error("Normalized context is not set.");
    }
    if (!this.onnxExec) {
      throw new Error("OnnxExec is not ready.");
    }
    const start = performance.now();

    this.Logger.write(LogLevel.DEBUG, `${logPrefix} Fetching processed code`);
    const end1 = performance.now();

    this.Logger.write(
      LogLevel.DEBUG,
      `${logPrefix} going into onnxExec.decode()`
    );

    let numAccepted = 0;
    let searchDepth = 0;
    let genTime = performance.now();
    for await (const pairedSuggestions of this.onnxExec.decodeGenerator(
      normalizedContext,
      this.settings.MAX_TOKENS_GENERATED,
      this.settings.CACHE_FREQUENCY,
      cancellationToken
    )) {
      if (cancellationToken.isCancellationRequested) {
        this.Logger.write(
          LogLevel.DEBUG,
          `${logPrefix} Cancelling within paired suggestion loop`
        );
        return RequestResult.Cancelled;
      }
      const beforeCacheTime = performance.now();
      numAccepted = await completionState.setLineCompletions(
        requestId,
        normalizedContext,
        pairedSuggestions,
        this.settings.getLogConfidence(lang),
        this.settings.WLC_RECOMMENDATION_LENGTH_PENALTY
      );
      searchDepth = pairedSuggestions[0][0].length;

      const newTime = performance.now();
      this.Logger.write(
        LogLevel.INFO,
        `${logPrefix} Process: ${end1 - start}\tDecode: ${
          newTime - genTime
        }\tCache: ${newTime - beforeCacheTime}`
      );
      genTime = newTime;
    }

    this.telemetry.sendTelemetryEvent(
      TelemetryEventNames.COMPLETION_REQUEST,
      {
        [TelemetryPropertyNames.LANGUAGE_ID]: lang || "unknown",
      },
      {
        [TelemetryMeasureNames.EVENT_DURATION]: performance.now() - end1,
        [TelemetryMeasureNames.NUM_REC]: numAccepted,
        [TelemetryMeasureNames.SEARCH_DEPTH]: searchDepth,
        [TelemetryMeasureNames.CONFIDENCE_THRESHOLD]:
          this.settings.CONFIDENCE_THRESHOLD,
      }
    );
    // return pairedSuggestions[0][1].join("");
    return RequestResult.Success;
  }

  public getFullName(): string {
    return "Line-level code completion service";
  }

  public getDefaultEndpoint(): string {
    return "0";
  }

  public getDefaultTimeout(): number {
    return 30000;
  }
}
