import { GraphData } from "./graphData";
// These constants are populated at build time by webpack, and are defined in webpack.config.js.
export function visualizationHtml(
  graphData: GraphData,
  aboveGraphText: string[]
) {
  const nodeString = JSON.stringify(graphData.nodes);
  const edgeString = JSON.stringify(graphData.edges);
  const optionsString = JSON.stringify(graphData.options);

  let insertAboveGraphText = "";

  for (let line of aboveGraphText) {
    insertAboveGraphText += `<div>${line}</div>`;
  }

  return `<!doctype html>
    <html>
    <head>
      <title>Network</title>
      <meta name="viewport" content="width=device-width, height=device-height, initial-scale=1.0">
      <script
        type="text/javascript"
        src="https://unpkg.com/vis-network@9.1.0/standalone/umd/vis-network.min.js"
      ></script>
      <style type="text/css">
        #mynetwork {
          width: 100%;
          height: 600%;
          border: 1px solid lightgray;
        }
      </style>
    </head>
    <body>
    ${insertAboveGraphText}
    <div id="mynetwork"></div>
    <script type="text/javascript">
      var nodes = new vis.DataSet(${nodeString});
      var edges = new vis.DataSet(${edgeString});
      var container = document.getElementById('mynetwork');
      var data = {
        nodes: nodes,
        edges: edges
      };
      var options = ${optionsString};
      var network = new vis.Network(container, data, options);
    </script>
    </body>
    </html>`;
}
