import { readFileSync, createReadStream } from "fs";
import { InferenceSession, Tensor, TypedTensor } from "onnxruntime-node";
import * as path from "path";
import * as unzipper from "unzipper";
import { extractCompletionPlaceholder } from "../../utils/document";
import { concat2d, filledTensor } from "../../utils/tensor";
import { END_OF_LINE_ID } from "../normalization";
//import Settings from "./settings";
import { ILoggingService, LogLevel } from "../logging";
import Settings from "../settings/settings";

const {
  LoadLocalModel,
  TextToIds,
} = require("../../lib/blingfire/blingfire_wrapper.js");
const { max_input_size } = require("../../model/metadata.json");

export type PairedSuggestions = [number[], string[]][];

const MAX_INPUT_SIZE = max_input_size;
interface OnnxInput {
  input_ids: Tensor;
  attention_mask: Tensor;
  position_ids: Tensor;
  beam_select_idx: Tensor;
  input_log_probs: Tensor;
  input_unfinished_sents: Tensor;
  [past: string]: Tensor;
  prev_step_results: Tensor;
  prev_step_scores: Tensor;
}

interface OnnxOutput {
  last_state: TypedTensor<"int64">;
  present_0: TypedTensor<"float32">;
  present_1: TypedTensor<"float32">;
  present_2: TypedTensor<"float32">;
  present_3: TypedTensor<"float32">;
  present_4: TypedTensor<"float32">;
  present_5: TypedTensor<"float32">;
  present_6: TypedTensor<"float32">;
  present_7: TypedTensor<"float32">;
  output_selected_indices: TypedTensor<"int64">;
  output_log_probs: TypedTensor<"float32">;
  output_unfinished_sents: TypedTensor<"bool">;
  current_step_results: TypedTensor<"int64">;
  current_step_scores: TypedTensor<"float32">;
}

class ModelInput {
  inputIds: TypedTensor<"int64">;
  positionIds: TypedTensor<"int64">;
  attentionMask: TypedTensor<"float32">;
  beamSelectIdx: TypedTensor<"int64">;
  inputLogProbs: TypedTensor<"float32">;
  inputUnfinishedSents: TypedTensor<"bool">;
  batchSize: number;
  initialInputLength: number;
  numLayer: number;
  beamSize: number;
  decodedFinalScore: TypedTensor<"float32"> | undefined;
  numAttentionHeads: number;
  past: Tensor[];
  prevStepResults: TypedTensor<"int64">;
  prevStepScores: TypedTensor<"float32">;

  done: boolean[];

  constructor(
    inputIds: number[],
    batchSize: number,
    numLayer: number,
    hiddenSize: number,
    numAttentionHeads: number,
    beamSize: number
  ) {
    this.batchSize = batchSize;
    this.initialInputLength = inputIds.length;

    const bigintInput = BigInt64Array.from(inputIds, (i) => BigInt(i));
    this.inputIds = new Tensor("int64", bigintInput, [
      this.batchSize,
      this.initialInputLength,
    ]);

    const positionArray = Array.from(Array(inputIds.length).keys()).map(BigInt);
    this.positionIds = new Tensor(
      "int64",
      Array(this.batchSize).fill(positionArray).flat(),
      [this.batchSize, inputIds.length]
    );

    const maskArray = Array(this.batchSize * this.initialInputLength).fill(1.0);
    this.attentionMask = new Tensor("float32", maskArray, [
      this.batchSize,
      this.initialInputLength,
    ]);

    this.beamSelectIdx = new Tensor(
      "int64",
      new BigInt64Array(this.batchSize),
      [this.batchSize, 1]
    );
    this.inputLogProbs = new Tensor("float32", Array(this.batchSize).fill(0), [
      this.batchSize,
      1,
    ]);
    this.inputUnfinishedSents = new Tensor(
      "bool",
      Array(this.batchSize).fill(1),
      [this.batchSize, 1]
    );
    this.prevStepResults = new Tensor("int64", bigintInput, [
      this.batchSize,
      this.initialInputLength,
    ]);
    this.prevStepScores = new Tensor("float32", Array(batchSize).fill(0), [
      this.batchSize,
      1,
    ]);

    this.numLayer = numLayer;
    this.beamSize = beamSize;

    this.numAttentionHeads = numAttentionHeads;
    const past_shape = [
      2,
      this.batchSize,
      this.numAttentionHeads,
      0,
      Math.floor(hiddenSize / this.numAttentionHeads),
    ];
    this.past = [];
    const pastTotalLength = past_shape.reduce((a, b) => a * b);
    for (let i = 0; i < numLayer; i++) {
      this.past.push(
        new Tensor("float32", Array(pastTotalLength).fill(0), past_shape)
      );
    }
    this.done = Array(batchSize).fill(0);
  }

  update(output: OnnxOutput, step: number) {
    const lastState = output.last_state;
    const lastDimSize = (lastState.size / (this.batchSize * this.beamSize)) | 0;
    this.inputIds = lastState.reshape([
      this.batchSize * this.beamSize,
      lastDimSize,
    ]);

    this.beamSelectIdx = output.output_selected_indices;
    this.inputLogProbs = output.output_log_probs;
    this.inputUnfinishedSents = output.output_unfinished_sents;
    this.prevStepResults = output.current_step_results;
    this.prevStepScores = output.current_step_scores;

    const newPosIds = Array(this.batchSize * this.beamSize).fill(
      BigInt(this.initialInputLength + step)
    );
    this.positionIds = new Tensor("int64", newPosIds, [
      this.batchSize * this.beamSize,
      1,
    ]);

    if (this.attentionMask.dims[0] != this.batchSize * this.beamSize) {
      this.attentionMask = new Tensor(
        "float32",
        Array(this.beamSize).fill(Array.from(this.attentionMask.data)).flat(),
        [this.batchSize * this.beamSize, this.attentionMask.dims[1]]
      );
    }
    this.attentionMask = concat2d(
      this.attentionMask,
      filledTensor([this.batchSize * this.beamSize, 1], "float32", 1),
      1
    );

    for (let i = 0; i < this.numLayer; i++) {
      // @ts-ignore
      this.past[i] = output[`present_${i}`];
    }
    this.done = this.done.map((d, i) => {
      return this.inputUnfinishedSents.data
        .slice(i * this.beamSize, i * this.beamSize + this.beamSize)
        .every((x) => !x);
    });
  }

  onnxInput(): OnnxInput {
    const input: OnnxInput = {
      input_ids: this.inputIds,
      attention_mask: this.attentionMask,
      position_ids: this.positionIds,
      beam_select_idx: this.beamSelectIdx,
      input_log_probs: this.inputLogProbs,
      input_unfinished_sents: this.inputUnfinishedSents,
      prev_step_results: this.prevStepResults,
      prev_step_scores: this.prevStepScores,
    };

    this.past.forEach((past, i) => {
      input[`past_${i}`] = past;
    });

    return input;
  }

  isDone() {
    return this.done.every((x) => x);
  }
}

export class OnnxModelWrapper {
  private onnxSession: InferenceSession;
  private vocab: string[];
  private bfModel: any;
  private beamSize: number;

  private numLayers: number;
  private numAttentionHeads: number;
  private embedDim: number;

  private settings: Settings;
  private Logger?: ILoggingService;

  private constructor(
    onnxSession: InferenceSession,
    modelConfig: any,
    bfModel: any,
    vocabPath: string,
    settings: Settings,
    loggingService?: ILoggingService
  ) {
    const vocabFileContent = readFileSync(vocabPath, "utf-8");
    this.vocab = vocabFileContent
      .split("\n")
      .map((line: string) =>
        line.substr(0, line.indexOf("\t")).replace(/\u2581/g, " ")
      );

    this.onnxSession = onnxSession;
    this.bfModel = bfModel;

    this.numLayers = modelConfig["n_layer"];
    this.numAttentionHeads = modelConfig["n_head"];
    this.embedDim = modelConfig["n_embd"];
    this.beamSize = modelConfig["beam_size"];
    this.settings = settings;
    this.Logger = loggingService;
  }

  public static async load(
    modelPath: string,
    configPath: string,
    tokenizerPath: string,
    vocabPath: string,
    settings: Settings,
    loggingService?: ILoggingService
  ): Promise<OnnxModelWrapper> {
    const sess = await InferenceSession.create(modelPath, {
      graphOptimizationLevel: "all",
      extra: {
        session: {
          intra_op: {
            allow_spinning: 0,
          },
        },
      },
    });
    const bfModel = await LoadLocalModel(tokenizerPath);

    const modelConfig = JSON.parse(readFileSync(configPath, "utf8"));

    return new OnnxModelWrapper(
      sess,
      modelConfig,
      bfModel,
      vocabPath,
      settings,
      loggingService
    );
  }

  public static async overwriteAndLoadFromZip(
    pathToZip: string,
    modelName: string = "composeModelQuantized.onnx",
    configName: string = "metadata.json",
    tokenizerName: string = "bpe.bin",
    vocabName: string = "vocab.txt",
    settings: Settings,
    loggingService?: ILoggingService
  ): Promise<OnnxModelWrapper> {
    const modelRoot = path.resolve(__dirname, "../../model");
    createReadStream(pathToZip).pipe(unzipper.Extract({ path: modelRoot }));

    const modelPath = path.join(modelRoot, modelName);
    const configPath = path.join(modelRoot, configName);
    const tokenizerPath = path.join(modelRoot, tokenizerName);
    const vocabPath = path.join(modelRoot, vocabName);

    return await OnnxModelWrapper.load(
      modelPath,
      configPath,
      tokenizerPath,
      vocabPath,
      settings,
      loggingService
    );
  }

  /**
   * Use decodeGenerator instead. Kept around for debugging purposes.
   * Will run a inference on the input up to maxLength token output.
   *
   * @see {@link decodeGenerator}
   * @param input
   * @param maxLength
   * @returns
   */
  public async decode(
    input: string,
    maxLength: number
  ): Promise<PairedSuggestions> {
    if (!input) {
      return [];
    }

    const inputLines = input.split(/\r?\n/g);
    let tokenIds: number[] = [];
    for (const [idx, line] of inputLines.entries()) {
      if (!line) {
        // The only way we should have an "empty" line is if have a new line at the end
        if (idx != inputLines.length - 1) {
          this.Logger?.write(
            LogLevel.WARN,
            `[OnnxModelWrapper.decodeGenerator()] ` +
              `Empty line encountered in middle of content. ` +
              `Check if normalization was done correctly:\n` +
              input
          );
        }
        continue;
      }

      tokenIds.push(...this.stringToIds(line));
      tokenIds.push(END_OF_LINE_ID);
    }
    if (!input.endsWith("\n")) {
      tokenIds.pop();
    }

    const lookback =
      this.settings.TOKEN_LOOKBACK + maxLength >= MAX_INPUT_SIZE
        ? MAX_INPUT_SIZE - maxLength
        : this.settings.TOKEN_LOOKBACK;
    this.Logger?.write(
      LogLevel.INFO,
      `[OnnxModelWrapper.decode()] Decoding with lookback: ${lookback}`
    );
    tokenIds = tokenIds.slice(-lookback);

    const modelInput = new ModelInput(
      tokenIds,
      1,
      this.numLayers,
      this.embedDim,
      this.numAttentionHeads,
      this.beamSize
    );

    let tokensGenerated = 0;
    for (; tokensGenerated < maxLength; tokensGenerated++) {
      const onnxOutput: any = await this.onnxSession.run(
        modelInput.onnxInput()
      );
      modelInput.update(onnxOutput, tokensGenerated);
      if (modelInput.isDone()) {
        tokensGenerated += 1;
        break;
      }
    }
    this.Logger?.write(
      LogLevel.INFO,
      `[OnnxModelWrapper.decode()] successfully generated ${tokensGenerated} tokens`
    );

    return this.createPairedSuggestions(modelInput, tokensGenerated);
  }

  /**
   * Async generator implementation of decode.
   * This version will yield partial results every yieldFrequency iterations of the model running.
   * It will always return the results from the last run even if it's not on time with yieldFrequency.
   *
   * About CancellationTokens:
   * Not honoring cancellation tokens was a decision made to avoid the problem of inconsistent results.
   * Taking cancellation into account led to the model producing a different number of tokens everytime.
   * Please consider this problem when thinking about this in the future.
   *
   * @param input Context to generate on
   * @param maxLength Max number of tokens to generate. Model may generate less than this number.
   * @param yieldFrequency Will yield partial results every yieldFrequency iteration
   * @returns Paired suggestions
   */
  public async *decodeGenerator(
    input: string,
    maxLength: number,
    yieldFrequency: number = 2,
    cancellationToken: { isCancellationRequested: boolean }
  ): AsyncGenerator<PairedSuggestions, PairedSuggestions | undefined, void> {
    const inputLines = input.split(/\r?\n/g);
    const tokenIds: number[] = [];
    for (const [idx, line] of inputLines.entries()) {
      if (!line) {
        // The only way we should have an "empty" line is if have a new line at the end
        if (idx != inputLines.length - 1) {
          this.Logger?.write(
            LogLevel.WARN,
            `[OnnxModelWrapper.decodeGenerator()] ` +
              `Empty line encountered in middle of content. ` +
              `Check if normalization was done correctly:\n` +
              input
          );
        }
        continue;
      }

      tokenIds.push(...this.stringToIds(line));
      tokenIds.push(END_OF_LINE_ID);
    }
    //  Due to us manually adding END_OF_LINE_ID in the line above for every loop,
    // we always end up w/ a \n character at the end.
    if (!input.endsWith("\n")) {
      tokenIds.pop();
    }

    if (cancellationToken.isCancellationRequested) {
      this.Logger?.write(
        LogLevel.INFO,
        `[OnnxModelWrapper.decodeGenerator()] Cancelling before preparing model input`
      );
      return undefined;
    }

    const lookbackCount =
      this.settings.TOKEN_LOOKBACK + maxLength >= MAX_INPUT_SIZE
        ? MAX_INPUT_SIZE - maxLength
        : this.settings.TOKEN_LOOKBACK;
    this.Logger?.write(
      LogLevel.INFO,
      `[OnnxModelWrapper.decodeGenerator()] Decoding with lookback: ${lookbackCount}`
    );
    const lookbackLengthTokenIds = tokenIds.slice(-lookbackCount);

    const modelInput = new ModelInput(
      lookbackLengthTokenIds,
      1,
      this.numLayers,
      this.embedDim,
      this.numAttentionHeads,
      this.beamSize
    );

    let tokensGenerated = 0;
    for (; tokensGenerated < maxLength; tokensGenerated++) {
      if (cancellationToken.isCancellationRequested) {
        this.Logger?.write(
          LogLevel.INFO,
          `[OnnxModelWrapper.decodeGenerator()] Cancelling in token generation loop iteration ${tokensGenerated}`
        );
        return undefined;
      }
      const onnxOutput: any = await this.onnxSession.run(
        modelInput.onnxInput()
      );
      modelInput.update(onnxOutput, tokensGenerated);
      if (modelInput.isDone()) {
        // If modelInput isDone(), we don't want to produce any more tokens
        // This will lead to multiline recommendations, which we don't want to support yet.
        yield this.createPairedSuggestions(modelInput, tokensGenerated + 1);
        break;
      } else if ((tokensGenerated + 1) % yieldFrequency === 0) {
        /**
         *  As our model currently runs beam search,
         * we have the problem of our results "narrowing down" when producing many tokens.
         * Which is why yielding results every once in a while allows consumers of the model
         * to view a wider variety of recommendations.
         *
         *  For example, for the first inference, our model will produce 4 different tokens.
         * [[<INDENT>],
         * [const],
         * [return],
         * [if]]
         *
         *  On the second inference the recommendations may look something like
         * [[<INDENT>, const],
         * [<INDENT>, return],
         * [<INDENT>, if],
         * [<INDENT>, let]]
         * Notice how although the 2nd token produced coincidentally looks similar to our set of 1st tokens,
         * the actual 1st token picked all converged to the <INDENT> token.
         *
         *  For our 5 token scenario, the beams may look like
         * [<INDENT>, if, (, context, .]
         * [<INDENT>, if, (, typeof, context]
         * [<INDENT>, if, (, context, ===]
         * [<INDENT>, if, (, context, &&]
         *
         *  Further down the road on 10 tokens produced, we'll see that for each beam of 10 tokens each,
         * the first 6~7 tokens have "converged" and are the same for every beam.
         *  Yielding every once in a while is to counteract this "convergence".
         * Trying ensure that we'll have something, albeit maybe being short, for different situations.
         */
        yield this.createPairedSuggestions(modelInput, tokensGenerated + 1);
      }
    }
    this.Logger?.write(
      LogLevel.INFO,
      `[OnnxModelWrapper.decodeGenerator()] successfully generated ${tokensGenerated} tokens`
    );

    // ensure that the async generator has a returned result at the end.
    return this.createPairedSuggestions(modelInput, tokensGenerated);
  }

  private createPairedSuggestions(
    resultingModelInput: ModelInput,
    numTokensGenerated: number
  ): PairedSuggestions {
    const tokenScores = resultingModelInput.prevStepScores;
    const sequences = resultingModelInput.prevStepResults;

    const recommendationCount = sequences.dims[0];
    const pairedSuggestions: PairedSuggestions = [];
    for (let i = 0; i < recommendationCount; i++) {
      const startOfNextLineTokens = (numTokensGenerated + 1) * (i + 1);
      const startOfNextLineRec = sequences.dims[1] * (i + 1);
      pairedSuggestions.push([
        Array.from(
          tokenScores.data.slice(
            startOfNextLineTokens - numTokensGenerated,
            startOfNextLineTokens
          ),
          (j) => Number(j)
        ),
        Array.from(
          sequences.data.slice(
            startOfNextLineRec - numTokensGenerated,
            startOfNextLineRec
          ),
          (j) => Number(j)
        ).map((j) => this.convertIdToToken(j)),
      ]);
    }

    return pairedSuggestions;
  }

  public randArray(len: number, min: number, max: number) {
    return Float32Array.from(
      { length: len },
      () => Math.floor(Math.random() * (max - min)) + min
    );
  }

  public convertIdToToken(id: number): string {
    return this.vocab[id];
  }

  public convertIdsToSeq(
    ids: number[],
    breaktokens: string[],
    speciaTokensMap: { [key: string]: string },
    specialCharsMap: { [key: string]: string }
  ): any {
    let result: string = " ";
    let index = 1;
    for (const id of ids) {
      let mid = "";
      mid = this.convertIdToToken(id);
      index = index + 1;
      if (specialCharsMap.hasOwnProperty(mid)) {
        mid = specialCharsMap[mid];
      }

      if (speciaTokensMap.hasOwnProperty(mid)) {
        mid = speciaTokensMap[mid];
      }

      if (breaktokens.indexOf(mid) !== -1) {
        result = result.replace(/▁/g, " ");

        result = extractCompletionPlaceholder(result).completionString;
        return result;
      }

      result += mid;
    }
    result = result.replace(/▁/g, " ");
    result = extractCompletionPlaceholder(result).completionString;
    return result;
  }

  public stringToIds(input: string): number[] {
    if (!input) {
      return [0];
    }
    return TextToIds(this.bfModel, input, input.length);
  }

  public tokenToId(token: string): number {
    if (!token) {
      return 0;
    }

    return TextToIds(this.bfModel, token, token.length)[0];
  }
}
