import { EOL } from "os";
import { performance } from "perf_hooks";
import { ICompletionResultCallback } from "./completionResultCallback";
import HashTrie from "./hashTrie";
import { ILoggingService, LogLevel } from "../services/logging";
import { END_OF_LINE } from "../services/normalization";
import { extractCompletionPlaceholder } from "../utils/document";
import { ITelemetrySender } from "../services/telemetry/sender";
import {
  TelemetryEventNames,
  TelemetryMeasureNames,
  TelemetryPropertyNames,
} from "../@types/telemetry";

const litTokenRegex = new RegExp("^<(NUM|STR|CHAR|REGEX)_LIT");
const punctuationSet = new Set([
  ".",
  ",",
  "/",
  "#",
  "!",
  "$",
  "%",
  "^",
  "&",
  "*",
  ";",
  ":",
  "=",
  "\\",
  "-",
  "~",
  "(",
  ")",
  "{",
  "}",
  "[",
  "]",
]);

export class CompletionState implements ICompletionResultCallback {
  public cacheTrie: HashTrie = new HashTrie();

  public constructor(
    private Logger?: ILoggingService,
    private Telemetry?: ITelemetrySender
  ) {}

  /**
   * Expects the scores inside completions to be raw cumulative scores.
   * However, when storing them, it'll store them as individual token scores.
   * @param normContext Normalized context to associate completions with
   * @param completions Completions in score-token pair format
   * @param logConfidenceThreshold If the length penalized last token score is under this threshold, that beam will not be stored in the cache
   * @param lengthPenalty A value larger than 1 rewards longer recommendations, and a value smaller than 1 penalizes it.
   * @returns Number of completions that were accepted into cache
   */
  public setLineCompletions(
    requestId: number,
    normContext: string,
    completions: [number | number[], string[]][],
    logConfidenceThreshold: number,
    lengthPenalty: number
  ): Promise<number> {
    const logPrefix = `[CompletionState.setLineCompletion() ${requestId}] `;
    let numAccepted = 0;
    const start = performance.now();

    for (let i = 0; i < completions.length; i++) {
      const [cumulativeScore, tokens] = completions[i];

      const cumulativeScoreArray = this.ensureCumulativeScoresAreArray(
        cumulativeScore,
        tokens.length
      );

      const [suggestions, confidence] = this.truncate(
        tokens,
        cumulativeScoreArray
      );

      // Check the final log confidence value
      const lastTokenLogConfidence = penalizedLogConfidence(
        confidence[confidence.length - 1],
        confidence.length,
        lengthPenalty
      );
      if (lastTokenLogConfidence < logConfidenceThreshold) {
        this.Logger?.write(
          LogLevel.INFO,
          `${logPrefix}Rejecting Suggestion:\n\t` +
            `Last penalized log confidence: ${lastTokenLogConfidence} < ${logConfidenceThreshold}\n\t` +
            `Suggestion: ${suggestions}`
        );
        continue;
      } else {
        this.Logger?.write(
          LogLevel.INFO,
          `${logPrefix}Accepting Suggestion:\n\t` +
            `Last penalized log confidence: ${lastTokenLogConfidence} >= ${logConfidenceThreshold}\n\t` +
            `Suggestion: ${suggestions}`
        );
      }

      // Suggestion is good. Prepare to cache.
      const individualScorePerToken = this.calculateIndividualScore(confidence);
      if (suggestions[suggestions.length - 1] === END_OF_LINE) {
        suggestions[suggestions.length - 1] = "\n"; // Change <endofline> into \n
      }

      let prefix = normContext;

      if (normContext.endsWith("\n")) {
        suggestions[0] = suggestions[0].trimLeft();
      }
      for (let i = 0; i < suggestions.length; i++) {
        const remainingSuggestions = suggestions.slice(i);
        const remainingScores = individualScorePerToken.slice(i);

        const currentSuggestion = remainingSuggestions[0];
        if (this.isMaskToken(currentSuggestion)) {
          this.cacheTrie.insert(prefix, remainingSuggestions, remainingScores);
          prefix += currentSuggestion;
        } else {
          for (let j = 0; j < currentSuggestion.length; j++) {
            this.cacheTrie.insert(
              prefix,
              remainingSuggestions,
              remainingScores
            );
            if (currentSuggestion) {
              prefix += currentSuggestion[j];
            }
            remainingSuggestions[0] = currentSuggestion.slice(j + 1);
          }
        }
      }

      numAccepted += 1;
    }
    const end = performance.now();
    this.Telemetry?.sendTelemetryEvent(
      TelemetryEventNames.CACHE_STORE,
      {},
      {
        [TelemetryMeasureNames.EVENT_DURATION]: end - start,
        // [TODO] Add memory consumption if possible
      }
    );
    return Promise.resolve(numAccepted);
  }

  /**
   * Returns best recommendation from cache.
   * Will return empty string if recommendation to short or doesn't exist.
   * @param context Normalized context to check cache for
   * @param firstTokenConfidenceThreshold Will only return a completion if the first token is above this confidence
   * @param logCutoffConfidence Will trucate recommendation when it falls below given confidence
   * @returns Recommendation and relevant metadata if in cache.
   */
  public getLineCompletion(
    context: string,
    firstTokenConfidenceThreshold: number,
    logCutoffConfidence: number
  ) {
    const start = performance.now();
    const [suggestedSuffix, firstTokenScore, cumulativeLastScore, tokenCount] =
      this.cacheTrie.getBestSuffix(
        context,
        firstTokenConfidenceThreshold,
        logCutoffConfidence
      );
    const bestSuffix = suggestedSuffix.replace(/\n/g, EOL);
    const end = performance.now();
    this.Telemetry?.sendTelemetryEvent(
      TelemetryEventNames.CACHE_ACCESS,
      {
        [TelemetryPropertyNames.CACHE_HIT]: (bestSuffix
          ? true
          : false
        ).toString(),
      },
      { [TelemetryMeasureNames.EVENT_DURATION]: end - start }
    );
    return {
      bestCompletion: bestSuffix,
      metadata: {
        firstTokenScore: firstTokenScore,
        cumulativeLastScore: cumulativeLastScore,
        tokenCount: tokenCount,
      },
    };
  }

  public getCacheTrieNode(prefix: string) {
    // prefix = normalizeCode(prefix);
    return this.cacheTrie.prefixMapping.get(prefix);
  }

  private calculateIndividualScore(cumulativeScores: number[]): number[] {
    let individualScorePerToken: number[] = [cumulativeScores[0]];
    for (let i = 1; i < cumulativeScores.length; i++) {
      individualScorePerToken.push(
        cumulativeScores[i] - cumulativeScores[i - 1]
      );
    }
    return individualScorePerToken;
  }

  private ensureCumulativeScoresAreArray(
    cumulativeScores: number | number[],
    completionLength: number
  ): number[] {
    if (typeof cumulativeScores === "number") {
      return new Array(completionLength).fill(cumulativeScores);
    }

    return cumulativeScores;
  }

  /**
   * Truncates the padded END_OF_LINE tokens away.
   * The first array returned is guaranteed to only have at most 1 END_OF_LINE token at the very end.
   * The second array will be the scores array truncated down to the same length.
   * @param tokens
   * @param tokenScores
   * @returns Both input arrays truncated
   */
  private truncate(
    tokens: string[],
    tokenScores: number[]
  ): [string[], number[]] {
    if (tokens.length !== tokenScores.length) {
      throw new Error(
        `[CompletionState.truncatePaddedEol()] Token/Confidence length mismatch. ` +
          `Check that the arrays passed in are of the same length`
      );
    }

    const truncationIndex = tokens.indexOf(END_OF_LINE);
    if (truncationIndex === -1) {
      // There are no EOLs, so truncate on last punctuation instead.
      const punctuationIndex = this.findLastPunctuation(tokens);
      if (punctuationIndex === -1) {
        return [tokens, tokenScores];
      } else {
        return [
          tokens.slice(0, punctuationIndex + 1),
          tokenScores.slice(0, punctuationIndex + 1),
        ];
      }
    }

    return [
      tokens.slice(0, truncationIndex + 1),
      tokenScores.slice(0, truncationIndex + 1),
    ];
  }

  /**
   * Checks the array to return the index of the last punctuation token.
   * @param tokens String array to check
   * @returns Index of the last punctuation. -1 if no punctuation.
   */
  private findLastPunctuation(tokens: string[]) {
    for (let i = tokens.length - 1; i >= 0; i--) {
      if (punctuationSet.has(tokens[i])) {
        return i;
      }
    }
    return -1;
  }

  /**
   * Heuristic way of checking if a token is a "mask token"
   * <unk>, <endofline>, <STR_LIT>, <REGEX_LIT> are examples of "mask tokens".
   *
   * @param s String to check
   * @returns Whether passed string is a mask token or not
   */
  private isMaskToken(s: string): boolean {
    return s.startsWith("<") && s.endsWith(">");
  }

  /**
   * Only expects "mask tokens" to be passed in.
   * Always check this.isMaskToken() before passing in string.
   *
   * NOTE: Always ignores following tokens.
   * <unk> <endoftext> <endoffunc> <DEDENT> <INDENT>
   * @param s Mask token to unmask
   * @returns Unmasked version of mask token
   */
  private getUnmaskedToken(s: string): string {
    if (litTokenRegex.test(s)) {
      return extractCompletionPlaceholder(s).completionString;
    }
    if (s === "<endofline>") {
      return "\n";
    }
    return "";
  }
}

/**
 * @param cumulativeScore Cumulative score of all tokens up to the target token
 * @param length How many tokens were produced including target token
 * @param lengthPenalty Length penalty. If this value > 1, shorter recommendations are penalized.
 * @returns Log confidence with length penalty applied
 */
export function penalizedLogConfidence(
  cumulativeScore: number,
  length: number,
  lengthPenalty: number
): number {
  return cumulativeScore / Math.pow(length, lengthPenalty);
}
