import { LanguageId } from "../../utils/language";
const { ConfidenceThreshold } = require("../../model/metadata.json");

export interface SettingsStorage {
  get<T>(section: string, key: string, defaultValue: T): T;
}
class Settings {
  constructor(private storage: SettingsStorage) {}

  public get LOG_LEVEL():
    | "ALL"
    | "DEBUG"
    | "INFO"
    | "WARN"
    | "ERROR"
    | "FATAL"
    | "OFF" {
    return this.storage.get("intellicodeCompletions", "loggingLevel", "ERROR");
  }

  public get TOKEN_LOOKBACK(): number {
    return this.storage.get(
      "intellicode-completions.internal",
      "maxInputSize",
      256
    );
  }

  public get MAX_TOKENS_GENERATED(): number {
    return this.storage.get(
      "intellicode-completions.internal",
      "maxTokensGenerated",
      10
    );
  }

  // Currently has no effect.
  // Model will generate no less than this value.
  public get CACHE_FREQUENCY(): number {
    return this.storage.get(
      "intellicode-completions.internal",
      "cacheFrequency",
      10
    );
  }

  public get CONFIDENCE_THRESHOLD(): number {
    return this.storage.get(
      "intellicode-completions.internal",
      "defaultConfidenceThreshold",
      ConfidenceThreshold
    );
  }

  public get LOG_CONFIDENCE_THRESHHOLD(): number {
    return Math.log(this.CONFIDENCE_THRESHOLD);
  }

  public readonly CUMULATIVE_PROBABILITY_THRESHOLD_PER_LANGUAGE: Map<
    string,
    number
  > = new Map<string, number>([
    [LanguageId.JAVA, this.CONFIDENCE_THRESHOLD],
    [LanguageId.JAVASCRIPT, this.CONFIDENCE_THRESHOLD],
    [LanguageId.JAVASCRIPT_REACT, this.CONFIDENCE_THRESHOLD],
    [LanguageId.PYTHON, this.CONFIDENCE_THRESHOLD],
    [LanguageId.TYPESCRIPT, this.CONFIDENCE_THRESHOLD],
    [LanguageId.TYPESCRIPT_REACT, this.CONFIDENCE_THRESHOLD],
  ]);

  public getConfidence(lang: string): number {
    if (!this.CUMULATIVE_PROBABILITY_THRESHOLD_PER_LANGUAGE.has(lang)) {
      throw new Error(lang + " does not have a confidence threshold defined");
    }
    const o = this.storage.get(
      "intellicode-completions.internal",
      "langSpecificCumulativeProbabilityThreshold",
      null /* specifically use null instead of undefined here, so that we cache the result null */
    ) as any as {
      [key in string]: number;
    };
    if (!o || !o[lang]) {
      return (
        this.CUMULATIVE_PROBABILITY_THRESHOLD_PER_LANGUAGE.get(lang) ||
        this.CONFIDENCE_THRESHOLD
      );
    }
    return o[lang];
  }

  public getLogConfidence(lang: string): number {
    return Math.log(this.getConfidence(lang));
  }

  public readonly DEFAULT_FIRST_TOKEN_THRESHOLD: number = -1.4;

  public readonly FIRST_TOKEN_THRESHOLD_PER_LANGUAGE: Map<string, number> =
    new Map<string, number>([
      [LanguageId.JAVA, this.DEFAULT_FIRST_TOKEN_THRESHOLD],
      [LanguageId.JAVASCRIPT, this.DEFAULT_FIRST_TOKEN_THRESHOLD],
      [LanguageId.JAVASCRIPT_REACT, this.DEFAULT_FIRST_TOKEN_THRESHOLD],
      [LanguageId.PYTHON, this.DEFAULT_FIRST_TOKEN_THRESHOLD],
      [LanguageId.TYPESCRIPT, this.DEFAULT_FIRST_TOKEN_THRESHOLD],
      [LanguageId.TYPESCRIPT_REACT, this.DEFAULT_FIRST_TOKEN_THRESHOLD],
    ]);

  public getFirstTokenConfidenceThreshold(lang: string): number {
    if (!this.FIRST_TOKEN_THRESHOLD_PER_LANGUAGE.has(lang)) {
      throw new Error(lang + " does not have a first token threshold defined");
    }

    const o = this.storage.get(
      "intellicode-completions.internal",
      "langSpecificFirstTokenThreshold",
      undefined
    ) as any as { [key in string]: number };
    if (!o || !o[lang]) {
      return (
        this.FIRST_TOKEN_THRESHOLD_PER_LANGUAGE.get(lang) ||
        this.DEFAULT_FIRST_TOKEN_THRESHOLD
      );
    }
    return o[lang];
  }

  public getLanguageEnabled(lang: string): boolean {
    if (lang === LanguageId.TYPESCRIPT_REACT) {
      return this.storage.get("intellicodeCompletions.language", LanguageId.TYPESCRIPT, false);
    } else if (lang === LanguageId.JAVASCRIPT_REACT) {
      return this.storage.get("intellicodeCompletions.language", LanguageId.JAVASCRIPT, false);
    } else {
      return this.storage.get("intellicodeCompletions.language", lang, false);
    }
  }

  public get WLC_RECOMMENDATION_LENGTH_PENALTY(): number {
    return this.storage.get(
      "intellicode-completions.internal",
      "recommendationLengthPenalty",
      1.01
    );
  }

  public get USE_WORKER_THREAD(): boolean {
    return this.storage.get(
      "intellicode-completions.internal",
      "inferenceWorkerThread",
      true
    );
  }

  public get BRACE_COMPLETION(): boolean {
    return this.storage.get(
      "intellicode-completions.internal",
      "braceCompletions",
      false
    );
  }

  public get PARSE_ERROR_FILTER(): boolean {
    return this.storage.get(
      "intellicodeCompletions",
      "parseErrorFilter",
      false
    );
  }

  public get SNIPPET_COMPLETION(): boolean {
    return this.storage.get(
      "intellicodeCompletions",
      "snippetCompletions",
      true
    );
  }
}

export default Settings;
