import * as vscode from "vscode";
import { visualizationHtml } from "./content";
import { GraphData } from "./graphData";

declare let __COMMIT_HASH__: string;
declare let __PACKAGE_VERSION__: string;

export async function showTreeWindow(
  title: string,
  graphData: GraphData,
  aboveGraphText: string[]
): Promise<void> {
  const panel = vscode.window.createWebviewPanel(
    "completion visualization",
    title,
    vscode.ViewColumn.Two,
    {
      enableScripts: true,
    }
  );

  aboveGraphText.unshift(
    `Extension version: ${__PACKAGE_VERSION__} ${__COMMIT_HASH__}`
  );

  panel.webview.html = visualizationHtml(graphData, aboveGraphText);
}
