import { MessagePort } from "worker_threads";
import { ILoggingService, LogLevel } from "../../services/logging";
import { LogMessage } from "../messages";

/**
 * Remote shim which implements ILoggingService by sending messages to the extension
 * telling it what to log.
 */
export class WorkerThreadLoggingService implements ILoggingService {
  constructor(private port: MessagePort) {}
  write(logLevel: LogLevel, str: string): void {
    const logMessage: LogMessage = {
      kind: "LogMessage",
      level: logLevel,
      message: str,
    };
    this.port.postMessage(logMessage);
  }
}
