/**
 *
 * @param completionString
 * @param snippet Will replace literal tokens w/ VSCode snippet notation if true. Else replace w/ default values.
 * @returns
 */
export function extractCompletionPlaceholder(
  completionString: string,
  snippet: boolean = false
) {
  const allMatchRegex = /<(NUM|STR|CHAR|REGEX)_LIT(?::(.+?))?>/;
  let snippetCount = 0;
  let next = completionString.match(allMatchRegex);
  while (next) {
    completionString = completionString.replace(
      allMatchRegex,
      (full, type, topLiteral) => {
        if (topLiteral) {
          const parsedTopLit = hardcodedParseUnicode(topLiteral);
          return type === "STR" ? `"${parsedTopLit}"` : `${parsedTopLit}`;
        }

        const defaultPlaceholder = type === "NUM" ? "0" : "";
        if (snippet) {
          snippetCount += 1;
          return type === "STR"
            ? `"$\{${snippetCount}:${defaultPlaceholder}}"`
            : `$\{${snippetCount}:${defaultPlaceholder}}`;
        } else {
          return type === "STR"
            ? `"${defaultPlaceholder}"`
            : `${defaultPlaceholder}`;
        }
      }
    );

    next = completionString.match(allMatchRegex);
  }
  return { completionString, snippetCount };
}

export function normalizeCode(code: string): string {
  return code
    .replace(/\".*?\"/gi, '""')
    .replace(/\'.*?\'/gi, "''")
    .replace(/ +/g, " ")
    .replace(/\r/g, "");
}

function hardcodedParseUnicode(s: string) {
  return s.replace(/U\+0020/g, " ").replace(/U\+002C/g, ",");
}

export function isAlphanumeric(charCode: number) {
  return (
    (charCode > 47 && charCode < 58) || // numeric (0-9)
    (charCode > 64 && charCode < 91) || // upper alpha (A-Z)
    (charCode > 96 && charCode < 123) // lower alpha (a-z)
  );
}

/**
 * VSCode's completion API requires that we recommend an entire replacement.
 * ex) If we want to suggest "string", and "str" is already typed out by the user,
 *    We still suggest "string" and not just "ing".
 *
 * However, our cache is currently designed so that it only remembers the remaining suffix.
 * Use this function to manually add back the prefix.
 *
 * @param codeBefore Searches for the prefix from here
 * @returns The last "word" at the end of codeBefore
 */
export function getPrefix(codeBefore: string) {
  for (let i = codeBefore.length; i > 0; i--) {
    const curCharNum = codeBefore.charCodeAt(i - 1);
    if (
      !(curCharNum > 47 && curCharNum < 58) && // numeric (0-9)
      !(curCharNum > 64 && curCharNum < 91) && // upper alpha (A-Z)
      !(curCharNum > 96 && curCharNum < 123) && // lower alpha (a-z)
      curCharNum !== 95 // underscore (_)
    ) {
      return codeBefore.slice(i);
    }
  }
  return "";
}
